  url("../fonts/NewBaskervilleC/NewBaskerville Bold-Italic.ttf") format("truetype"),
  url("../fonts/NewBaskervilleC/NewBaskervilleC-BoldItalic.svg#NewBaskervilleC-BoldItalic") format("svg");
