.soc-bl{
  &__title{
    font-size: 14px;
    color:#545454;
    margin-bottom: 24px;
  }
  a{
    display: inline-block;
    margin-right: 10px;
    width: 32px;
    height: 31px;
    opacity: 0.33;
    transition: all 0.3s ease;
    /*&:hover{
      &.icon-soc-footer1,
      &.icon-soc-footer2,
      &.icon-soc-footer3,
      &.icon-soc-footer4,
      &.icon-soc-footer5,
      &.icon-soc-footer6{
        background-image: url("../images/sprite.png");
        width: 32px;
        height: 31px
      }
    }*/
    &:last-child {
      margin-right: 0;
    }
    &:hover{
      opacity: 1;
    }
    .icon{
      display: block;
      width: 32px;
      height: 31px;
      background-size: cover;
      cursor: pointer;
      &.icon-soc-footer1, &.icon-soc-footer2, &.icon-soc-footer3, &.icon-soc-footer4{
        //background-image: url("../images/sprite.png");
      }
    }
    .icon-soc-footer1{
      background: url("../images/icon-fc.png") 50% 50%;
    }
    .icon-soc-footer2{
      background: url("../images/icon-tw.png") 50% 50%;
    }
    .icon-soc-footer3{
      background: url("../images/icon-vk.png") 50% 50%;
    }
    .icon-soc-footer4{
      background: url("../images/icon-ok.png") 50% 50%;
    }
    .icon-soc-footer5{
      background: url("../images/icon-yt.png") 50% 50%;

    }
    .icon-soc-footer6{
      background: url("../images/icon-insta.png") 50% 50%;
    }
  }

}

