.search{
  &__form{
    margin-bottom: 18px;
  }
  &__info{
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 10px;
    text-transform: uppercase;
    letter-spacing: 2px;
    color:#aeaeae;
    margin-bottom: 50px;
  }
  &__form-inner{
    position: relative;

  }
  &__form-input{
    position: relative;
    width: 100%;
    height: 50px;
    line-height: 49px;
    background: #f6f6f6;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 16px;
    color:$black;
    padding: 0 40px 0 20px;
  }
  &__form-submit{
    position: absolute;
    width: 40px;
    background: url("../images/lupa-orange.png") no-repeat left center;
    right: 0;
    top:0;
    bottom: 0;
  }
  &__result{
   counter-reset: list1;
   margin-bottom: 53px;
 }
  &__result-item{

   position: relative;
   padding-left: 47px;
   padding-top: 3px;
    margin-bottom: 33px;
   &:before{
     counter-increment: list1; /*    */
     content: counter(list1); /*   */
     position: absolute;
     left: 0;
     top:0;
     width: 32px;
     height: 32px;
     border:1px solid $orange;
     border-radius: 50%;
     display: inline-flex;
     justify-content: center;
     align-items: center;
     font-family: $HelveticaNeueRegular;
     font-weight: normal;
     font-size: 14px;
     color:$orange;
   }


 }
  &__result-title{
    font-family: $HelveticaNeueLight;//$HelveticaNeueRegular
    font-weight: 300;
    font-size: 24px;
    line-height: 26px;
    color:$orange;
    margin-bottom: 22px;
  }
  &__result-desc{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 16px;
    line-height: 26px;
    color:$black;
  }
  @media (min-width:1200px) {
    &__btn{
      display: none;
    }
  }
  @media (max-width:767px) {
    &__info{
      margin-bottom: 39px;
    }
    &__result{
      margin-bottom: 40px;
    }
  }
}
