#map{
  height: 443px;
}
.contact{
  &__map{
    margin-bottom: 12px;
  }
  &__items{
    margin-bottom: 60px;
    max-width: 100%;
    overflow-x: hidden;
  }
  @media (max-width:767px) {
    /*&__map{
      margin-bottom: 12px;
    }*/
    &__items{
      margin-bottom: 40px;
    }

  }
}

.contact-item{
  padding-top: 29px;
  padding-bottom: 29px;
  border-bottom: 1px solid #f6f6f6;
  align-items: center;
  &__city{
    font-family: $HelveticaNeueThin;
    font-weight: 100;
    font-size: 35px;
    color:$black;

  }
  &__info{
    font-size: 16px;
    color: $black;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    -webkit-font-smoothing: antialiased;
  }
  &__info-company{
    font-family: $HelveticaNeueBold;
    font-weight: bold;
    margin-bottom: 2px;
  }
  &__callback{}
  &__callback-phone-it{
    margin-bottom: 3px;
  }
  &__callback-phone{
    display: inline-block;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 24px;
    color:$black;

    &:hover{
      color:$orange;
    }
  }
  &__callback-email{
    display: block;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 16px;
    color:$orange;
    span{
      position: relative;
      &:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        background: $orange;
        bottom: 0;
        left: 0;
        transition: width 0.3s ease;
      }
    }
    &:hover{
      color:$orange;
      span{
        &:before{
          width: 0;
        }
      }
    }
  }
  @media (max-width: 1199px) {

  }
  @media (max-width: 991px) {
    &__city{
      font-size: 26px;
      font-family: $HelveticaNeueLight;
      font-weight: 300;
    }
    &__callback-phone{
      font-size: 19px;
    }
  }
  @media (max-width: 767px) {
    padding-top: 25px;
    padding-bottom: 26px;
    &.active_block{
      .contact-item__city{
        &:before{
          opacity: 0;
        }
        &:after{
          opacity: 1;
        }
      }
    }
    &__city{
      font-size: 24px;
      font-family: $HelveticaNeueRegular;
      font-weight: normal;
      position: relative;
      &:before{
        content: "";
        position: absolute;
        width: 19px;
        height: 19px;
        right: 10px;
        top: 0;
        bottom: 0;
        margin: auto;
        background-size: cover;
        border-radius: 50%;
        background: url("../images/icon-circlePlus.png") no-repeat 50%;
      }
      &:after{
        content: "";
        position: absolute;
        width: 19px;
        height: 19px;
        right: 10px;
        top: 0;
        bottom: 0;
        margin: auto;
        background-size: cover;
        border-radius: 50%;
        background: url("../images/icon-circleMinus.png") no-repeat 50%;
        opacity: 0;
      }
    }
    &__callback-phone{
      font-size: 24px;

    }
    &__callback{
      display: none;
    }
    &__info{
      margin-top: 26px;
      margin-bottom: 21px;
      display: none;
    }
  }
}
.play-button{
  position: relative;
  z-index: 100;
}
