#page{
  //min-height: 100vh;
  &.menu-active{
    position: relative;
    min-height: 100vh;
    &:before{
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background: #161616;
      opacity: 0.87;
      z-index: 3;
    }
  }
}

//menu detail
.menu-detail{
  background: #161616;
  padding-bottom: 24px;
  padding-top: 32px;
  display: none;
  max-height: 100vh;
  overflow-y: auto;

  @media (max-width:991px) {
    padding-top: 0;
    .container{
      height: 100vh;
      overflow-y: auto;
      margin-bottom: 70px;

    }
    .header__search{
      display: block;
      width: 100%;
      height: 45px;
      .search-form{
        width: 100%;
        position: relative;

        label{
          width: 100%;
        }
      }
      .search-field{
        width: 100%;
        background-color: $white;
        background-image: url(../images/magnifying-glass-orange.svg);
        background-repeat: no-repeat;
        background-position: center right 20px;
        padding-left: 20px;
        height: 45px;
      }
    }
  }

}
.menu-detail{
  color: $white;
  margin: auto;
  @media (min-width:768px) {
    .header__search{
      display: none;
    }
  }
}
.menu-detail-item{
  &__title{
    font-family: $HelveticaNeueLight;
    font-size: 24px;
    color: #acacac;
    margin-bottom: 15px;
  }
  &__list{
    li{
      a{
        display: inline-block;
        font-size: 14px;
        line-height: 18px;
        color: $white;
        padding: 17px 0 16px;
        transition: all 0.5s ease;
        &:hover{
          color:#ff815c;
          text-decoration: none;
        }
      }
    }

  }
  @media (max-width:991px) {
    //max-width: 291px;
    &.active_block{
      border-bottom: 1px solid #2b2b2b;
      .menu-detail-item__title{
        &:after{
          opacity: 0;
        }
      }
    }
    &__title{
      position: relative;
      border-bottom: 1px solid #2b2b2b;
      margin-bottom: 0;
      padding: 22px 0 21px;
      &:before{
        content: "";
        position: absolute;
        right: 0;
        background: url("../images/minus-white.png");
        width: 11px;
        height: 1px;
        top: 0;
        bottom: 0;
        margin: auto;
      }
      &:after{
        content: "";
        position: absolute;
        background: url("../images/minus-white.png");
        width: 1px;
        height: 11px;
        transform: rotate(180deg);
        right: 5px;
        top: 0;
        bottom: 0;
        margin: auto;
      }

    }
    &__list{
      display: none;
    }
  }
  @media (min-width: 768px) and (max-width: 991px){

  }
  @media (max-width:767px) {

  }
}

.menu-detail-contact{
  margin-top: -2px;
  &__title{
    font-family: $HelveticaNeueMedium;
    font-size: 10px;
    line-height: 36px;
    letter-spacing: 2px;
    color: #acacac;
    text-transform: uppercase;
  }
  &__text{
    font-family: $HelveticaNeueLight;
    font-size: 24px;
    line-height: 30px;
    color: $white;
    margin-bottom: 26px;
  }
  &__phone{
    display: block;
    font-family: $HelveticaNeueLight;
    font-size: 24px;
    line-height: 30px;
    color: $white;
    transition: all 0.5s ease;
    margin-bottom: 46px;
    &:hover{
      color:#ff815c;
      text-decoration: none;
    }
  }
  &__callback{
    display: block;
    width: 100%;
    height: 50px;
    line-height: 50px;
    background: transparent;
    border: 1px solid #ff815c;
    font-family: $HelveticaNeueMedium;
    font-size: 10px;
    letter-spacing: 2px;
    color: $white;
    text-transform: uppercase;
    text-align: center;
    transition: all 0.5s ease;
    margin-bottom: 47px;
    &:hover{
      background: #ff815c;
      color:$white;
      text-decoration: none;
    }
  }
  @media (min-width: 768px) and (max-width: 991px) {
    padding-left: 53px;
    position: relative;
    height: 100%;
    &:before{
      content: "";
      position: absolute;
      width: 1px;
      height: calc(100% - 16px);
      background: #2b2b2b;
      left: 12px;
    }
    &__text{
      margin-bottom: 17px;
    }
    &__title{
      line-height: 26px;
    }
    .soc-bl{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      &__title{
        width: 100%;
      }
    }
  }
  @media (max-width:991px) {
    //max-width: 291px;
    margin-top: 18px;
    &__phone{
      margin-bottom: 26px;
    }
    &__callback{
      margin-bottom: 26px;
    }
  }
}

.menu-top{
  height: 100%;
  &__list{
    margin-top: 3px;
    li{
      margin-bottom: 33px;
      &:last-child{
        margin-bottom: 0;
      }
      a{
        font-family: $HelveticaNeueRegular;
        font-size: 14px;
        color:$white;
        font-weight: normal;
        padding: 7px 0;
      }
    }
  }
  @media (max-width:991px) {
    margin-top: 25px;
    padding-left: 53px;
    position: relative;
    &:before{
      content: "";
      position: absolute;
      width: 1px;
      height: calc(100% - 28px);
      background: #2b2b2b;
      left: 12px;
    }
  }
}

@media all and (max-width:1150px) {
  .wrapMenu .topMenu {
    width: 100%;
  }
}
@media (min-width:1151px) {
  .wrapMenu .topMenu{
    width:1170px;
  }
}


