.page-reviews{
  @media (min-width: 398px) and (max-width: 767px) {
    .header-title{
      text-align: center;
    }
  }
}
.reviews-item{
  margin-bottom: 35px;
  &__foto{
    margin-bottom: 28px;
    overflow: hidden;
    img{
      transition: all 0.3s ease;
    }
  }
  &__title{
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 12px;
    color:#f28058;
    text-transform: uppercase;
    margin-bottom: 18px;
  }
  &__subtitle{
    font-family: $HelveticaNeueRegular;
    font-weight: 400;
    font-size: 24px;
    color:$black;
    margin-bottom: 22px;
  }
  &__desc{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color:$black;
  }
  &:hover{
    .reviews-item__foto{
      img{
        transform: scale(1.05);
      }
    }
  }
  @media (max-width:767px) {
    max-width: 393px;
    margin-left: auto;
    margin-right: auto;
    &__foto{
      margin-bottom: 18px;
      img{
        width: 100%;
      }
    }
    &__title{
      margin-bottom: 4px;
    }
    &__subtitle{
      margin-bottom: 11px;
    }
  }

}
