.services-detail-stages{
  background: #f6f6f6;
  padding-top: 55px;
  padding-bottom: 55px;
  margin-bottom: 58px;
  .h2-page{
    color: #ff8155 !important;
    margin-bottom: 54px;
  }
  &__item{
    display: flex;
    flex-wrap: wrap;
    position: relative;
    &:before{
      content:"";
      position: absolute;
      height: 1px;
      background: #f37854;
      top:26px;
    }
    &:after{
      content:"";
      position: absolute;
      background: #f37854;
      width: 5px;
      height: 5px;
      border-radius: 50%;
      top:24px;

    }
    .number{
      width: 50px;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      color: #f37854;
      border: 1px solid #f37854;
      text-align: center;
    }
    .description{
      max-width: 218px;
      color: $black;
      margin-left: 15px;
      font-family: $HelveticaNeueRegular;//$HelveticaNeueLight
      &__title{
        font-size: 24px;
        line-height: 24px;
        margin-bottom: 25px;
        max-width: 200px;
      }
      &__text{
        font-size: 16px;
        line-height: 24px;
      }
    }
    &:first-child{
      &:before, &:after{
        opacity: 0;
      }
    }
    &:nth-child(2){
      &:before{
        width: 90px;
        left: -90px;
      }
      &:after{
        left: -95px;
      }
    }
    &:nth-child(3), &:nth-child(4){
      &:before{
        width: 61px;
        left: -61px;
      }
      &:after{
        left: -66px;
      }
    }
    &:last-child{
      .description{
        &__title{
          max-width: unset;
        }
      }
    }
  }
  @media (min-width:992px) and (max-width: 1199px) {
    &__item{
      width: 48%;
      margin-bottom: 33px;
      &:before, &:after{
        opacity: 0;
      }
      .description{
        max-width: calc(100% - 110px);
      }
      &:nth-child(3){
        &:before, &:after{
          opacity: 0;
        }
      }
      &:nth-last-child(-n+2){
        margin-bottom: 0;
      }
      &:nth-child(even){
        .description{
          max-width: calc(100% - 80px);
        }
      }
    }
  }
  @media (max-width:991px) {
    padding-top: 33px;
    padding-bottom: 40px;
    .h2-page{
      margin-bottom: 33px;
    }
    &__item{
      margin-bottom: 27px;

      &:before{
        content:"";
        position: absolute;
        width: 1px;
        left: 19px;
        height: 60px;
        background: #f37854;
        top:-60px;
      }
      &:after{
        content:"";
        position: absolute;
        background: #f37854;
        width: 5px;
        height: 5px;
        border-radius: 50%;
        top:-65px;
        left: 17px;

      }
      &:nth-child(2), &:nth-child(3), &:nth-child(4){
        &:before{
          content:"";
          position: absolute;
          width: 1px;
          left: 19px;
          height: 60px;
          background: #f37854;
          top:-60px;
        }
        &:after{
          content:"";
          position: absolute;
          background: #f37854;
          width: 5px;
          height: 5px;
          border-radius: 50%;
          top:-65px;
          left: 17px;

        }
      }

      &:last-child{
        .description{
          &__title{
            max-width: unset;
          }
        }
      }
      .description{
        max-width: calc(100% - 60px);
      }
      .number{
        width: 40px;
        height: 40px;
      }
    }


  }
  @media (min-width: 768px) and (max-width: 991px) {
    &__item{
      .description{
        &__title{
          max-width: 100%;
        }
      }
    }
  }
  @media (max-width:767px) {
    padding-bottom: 9px;
    margin-bottom: 0px;
  }
}
