.slider-intext{
  width: 100vw;
  padding-left: 80px;
  position: relative;
  &:before{
    content: "";
    position: absolute;
    height: 70px;
    right: 0;
    width: calc(100vw - 987px);
    background: #fff;
    bottom: 1px;
    z-index: 2;
  }
  .slick-slide{

    .slider-intext__item{

    }
    &.slick-current{
      .slider-intext__item{
        .img{
          &:before{
            opacity: 0;
          }
        }
      }
      .img-point{
        opacity: 1;
        pointer-events: auto;
      }
    }
  }
  .slick-arrow{
    text-align: center;
    bottom: 29px;
    top:auto;
    position: absolute;
    z-index: 4;
    &.prevArrow{
      display: inline-block;
      width: 40px;
      height: 25px;
      vertical-align: middle;

      left:988px;
      //left: auto;
      background: url("../images/slider-intext-prev.png") left center no-repeat;
    }
    &.nextArrow{
      display: inline-block;
      width: 40px;
      height: 25px;
      vertical-align: middle;
      background: url("../images/slider-intext-next.png") left center no-repeat;
      //right:252px;
      left: 1138px;
    }
    &:hover{
      cursor: pointer;
    }
  }
  .slick-dots{
    top: auto;
    bottom: 26px;
    //right: 300px;
    left: 1056px;
    counter-reset: slides-num; /* Initialize counter. */
    position: absolute;
    margin-top: 15px;
    width: 60px;
    z-index: 5;
    /* &:before{
       content:"  /";
       font-family: $HelveticaNeueLight;
       display: inline-block;
       vertical-align: middle;
       font-size: 24px;
       line-height: 30px;
       position: absolute;
       left: 2px;//38%
       top: 4px;

     }*/
    &:after{
      font-family: $HelveticaNeueLight;
      font-size: 24px;
      line-height: 30px;
      font-weight: 800;
      color:green;
      content: counter(slides-num);
      display: inline-block;
      vertical-align: middle;
    }
    li{
      position: absolute;
      //right: 60px;
      left: 0;
      counter-increment: slides-num;
      pointer-events: auto;
      font-family: $HelveticaNeueLight;
      button {
        display: none !important;
      }
      &.slick-active {
        &:before {
          content: counter(slides-num);
          font-family: $HelveticaNeueLight;
          display: inline-block;
          vertical-align: middle;
          font-size: 24px;
          line-height: 30px;
          position: absolute;
          left: 0;
          top: 0;
          color: #f37854;
        }
      }
    }
    &:after{
      font-family: $HelveticaNeueLight;
      font-size: 24px;
      line-height: 30px;
      font-weight: 600;
      color:$black;
      content: "/" counter(slides-num);
      display: inline-block;
      vertical-align: middle;
    }
  }
  &__title{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 12.34375vw;
    z-index: 3;
    font-family: $HelveticaNeueLight;
    font-size: 35px;
    line-height: 35px;
    letter-spacing: 20px;
    text-transform: uppercase;
    color: $black;
    span{
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 28px;
      transform: translateX(-50%) translateY(-50%) rotate(-90deg);
    }
    @media (max-width:991px) {
      position: relative;
      width: 100%;
      font-size: 30px;
      letter-spacing: 12px;
      text-align: center;
      margin-bottom: 33px;
      span{
        transform: none;
        position: relative;
        left: auto;
        top: auto;
      }
    }
  }
  &__item{
    padding: 0 6px;

    .img{
      position: relative;
      &:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background:$black;
        opacity: 0.4;
      }
    }

  }
  @media (min-width: 992px) and (max-width: 1239px) {
    width: 100%;
    &:before{
      display: none;
      width: 0;
    }
    .slick-arrow{
      bottom: 0;
      &.prevArrow{
        left: 0;
        right: 0;
        margin: auto;
        transform: translateX(-70px);
      }
      &.nextArrow{
        left: 0;
        right: 0;
        margin: auto;
        transform: translateX(56px);
      }
    }
    .slick-dots{
      left: 0;
      right: 0;
      margin: auto;
      bottom: 0;
    }
    padding-bottom: 45px !important;
  }
  @media (max-width:991px) {
    width: 100%;
    padding-left: 0;
    padding-bottom: 45px !important;
    &:before{
      display: none;
      width: 0;
    }
    .slick-arrow{
      bottom: 0;
      &.prevArrow{
        left: 0;
        right: 0;
        margin: auto;
        transform: translateX(-70px);
      }
      &.nextArrow{
        left: 0;
        right: 0;
        margin: auto;
        transform: translateX(56px);
      }
    }
    .slick-dots{
      left: 0;
      right: 0;
      margin: auto;
      bottom: 0;
    }
  }

}

.img-point{
  position: absolute;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #ff7951;
  transition: all 0.5s ease;
  opacity: 0;
  pointer-events: none;
  &:before{
    content: "";
    position: absolute;
    width: 12px;
    height: 1px;
    background: $white;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    transition: transform .8s ease, -webkit-transform .8s ease;
  }
  &:after{
    content: "";
    position: absolute;
    width: 12px;
    height: 1px;

    background: $white;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    transform: rotate(90deg);
    transition: transform .8s ease, -webkit-transform .8s ease;
  }
  .img-point__text{
    background: $black;
    min-width: 7.813vw;
    position: absolute;
    padding: 16px 20px 14px;
    top: 100%;
    left: 100%;
    pointer-events: none;
    opacity: 1;
    transform: translateX(-50%) translateY(-50%) scale(0);
    transition: transform .8s ease, -webkit-transform .8s ease;
    .name{
      font-family: $HelveticaNeueMedium;
      font-size: 16px;
      line-height: 24px;
      color: $white;
      white-space: nowrap;
    }
    .country{
      font-family: $HelveticaNeueLight;
      font-size: 16px;
      line-height: 24px;
      color: $white;
      white-space: nowrap;
    }
  }
  &:hover{
    cursor: pointer;
    &:before{
      transform: rotate(-180deg);
    }
    &:after{
      transform: rotate(-90deg);
    }
    .img-point__text{
      transform: none;
    }

  }
  @media (max-width:991px) {
    display: none;
  }
}
.b-r{
  .img-point__text{
    right: 100%;
    left: auto;
    transform: translateX(50%) translateY(-50%) scale(0);
  }
}

