  }
  &__submit{
    width: 240px;
    background: #932d0a;
    height: 50px;
    line-height: 49px;
    border: 1px solid #932d0a;
    margin-left: 20px;
    &:hover{
      background: transparent;
      color: $white;
    }
  }
  &__personal{
    width: 100%;
    padding-left: 20px;
    position: relative;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 12px;
    color: #87443a;
    margin-top: 23px;
