.page-history-house-detail{
  .slider-exterior{
    margin-bottom: 55px;
  }
  .services-specifications__items{
    margin-top: 55px;
  }
  @media (max-width:767px) {
    .slider-exterior{
      margin-bottom: 37px;
    }
    .services-specifications__items{
      margin-top: 37px;
      margin-bottom: 46px !important;
    }
    .services-detail-projects{
      margin-bottom: 33px;
    }
  }

}
.header-history-house-detail{
  height: 518px;
  margin-top: 0;
  padding-top: 107px;
  margin-bottom: 63px;
  align-items: flex-end;
  &__title{
    &.h2{
      font-family: $HelveticaNeueThin;
      font-weight: 100;
      color:$white;
      text-align: center;
      margin-bottom: 18px;
    }
  }
  @media (max-width:767px) {
    margin-bottom: 33px;
    height: 311px;
    &__title{
      &.h2{
        margin-bottom: 12px;
      }
    }
  }
}

.history-house-detail-itog{
  border-top: 1px solid rgba($white,0.4);
  padding-top: 30px;
  &__item{
    text-align: center;
    margin-bottom: 24px;
    .top{
      font-family: $HelveticaNeueRegular;
      font-size: 35px;
      line-height: 35px;
      color:$white;
      height: 40px;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      margin-bottom: 9px;
      span{
        font-size: 16px;
        line-height: 1.2;
        sup{
          font-size: 10px;
        }
      }
    }
    .text{
      font-family: $HelveticaNeueRegular;
      font-size: 12px;
      line-height: 24px;
      text-transform: uppercase;
      color:$white;
    }
  }
  @media (max-width:991px) {
    padding-bottom: 29px;

  }
  @media (max-width:767px) {
    padding-top: 12px;
  }
}

.history-house-detail-3d{
  margin-bottom: 51px;
  &__title{
    font-family: $HelveticaNeueThin;
    font-weight: 100;
    font-size: 35px;
    color:$black;
    margin-bottom: 61px;
  }
  &__text{
    font-family: $HelveticaNeueRegular;
    font-size: 16px;
    line-height: 30px;
    color: $black;
    margin-bottom: 51px;
    p{
      margin-bottom: 32px;
    }
  }
  @media (max-width:1199px) {
    &__title{
      font-size: 32px;
    }
  }
  @media (max-width:991px) {

  }
  @media (max-width:767px) {
    margin-bottom: 37px;
    &__title{
      font-size: 24px;
      line-height: 30px;
      margin-bottom: 37px;
    }
  }
}

.hhd-3d-items{
  margin-bottom: 52px;
  justify-content: center;
  &.itemb5{
    justify-content: flex-start;
  }
  @media (max-width:767px) {
    margin-bottom: 33px;
  }
  @media (min-width:992px) {
    &.itemb5{

      .hhd-3d-item{
        &:first-child{
          &:after{
            opacity: 0;
          }
        }
        &:nth-child(5n+1){
          position: relative;
          &:after{
            content: "";
            position: absolute;
            width: calc(100%*5);
            height: 1px;
            background: #e3633d;
            top:0;
            left: 0;
          }
        }
        &:nth-child(5n){
          &:before{
            opacity: 0;
          }
        }
        &:last-child{
          &:before{
            opacity: 1;
          }
        }
      }
    }
  }
  @media (min-width: 768px) and (max-width: 991px){
    &.itemb5{

      .hhd-3d-item{
        &:first-child{
          &:after{
            opacity: 0;
          }
        }
        &:nth-child(4n+1){
          position: relative;
          &:after{
            content: "";
            position: absolute;
            width: calc(100%*4);
            height: 1px;
            background: #e3633d;
            top:0;
            left: 0;
          }
        }
        &:nth-child(4n){
          &:before{
            opacity: 0;
          }
        }
        &:last-child{
          &:before{
            opacity: 1;
          }
        }
      }
    }
  }
}
.hhd-3d-item{
  //flex: 0 0 20%;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 17px;
  position: relative;
  transition: all 0.5s ease;
  min-width: 20%;
  &:before{
    content: "";
    position: absolute;
    width: 1px;
    height: 100%;
    background: #e3633d;
    top: 0;
    right: 0;
  }
  &__iframe{
    display: none;
    padding: 0;
    background: transparent;
    border: 0;
    outline: 0;
    .fancybox-close-small{
      display: none;
    }
  }

  &__icon{
    margin-bottom: 18px;
  }
  &__text{
    font-family: $HelveticaNeueMedium;
    font-size: 16px;
    line-height: 1.2;
    color: $white;
    text-transform: uppercase;
  }
  &:last-child{
    &:before{
      opacity: 0;
    }
  }
  &:hover, &:active{
    background: #e3633d;
    color:$white;
    text-decoration: none;
  }
  @media (min-width:992px) {

  }
  @media (min-width: 768px) and (max-width: 991px){
    min-width: 25%;
  }
  @media (max-width:767px) {
    min-width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    text-align: left;
    padding-top: 20px;
    padding-bottom: 17px;
    position: relative;
    transition: all 0.5s ease;
    &:after{
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      background: #e3633d;
      bottom: 0;
    }
    &__icon{
      flex: 0 0 60px;
      margin-bottom: 0;
      text-align: center;
      img{
        height: 35px;
      }
    }
    &__text{
      width: calc(100% - 60px);
      font-family: $HelveticaNeueMedium;

      font-size: 16px;
      line-height: 1.2;
      color: $white;
      text-transform: none;
    }
    &:last-child{
      &:before{
        opacity: 0;
      }
    }
    &:hover{
      background: #e3633d;
      text-decoration: none;
    }
  }
  @media (min-width:550px) and (max-width: 767px) {
    min-width: 50%;
    &:nth-child(odd){
      &:before{
        content: "";
        position: absolute;
        width: 1px;
        height: 100%;
        background: #e3633d;
        bottom: 0;
      }
    }
    &:nth-child(even){
      &:before{
        opacity: 0;
      }
    }
    &:last-child{
      &:before{
        opacity: 1;
      }
    }
  }
}

.history-house-detail-plaining{
  padding-top: 51px;
  padding-bottom: 56px;
  margin-bottom: 60px;
  &__title{
    margin-bottom: 23px;
  }
  &__text{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: $black;
    margin-bottom: 16px;
  }
  &__area{
    border-top: 1px solid $white;
    border-bottom: 1px solid $white;
    font-family: $HelveticaNeueRegular;
  }
  &__img{
    display: block;
    text-align: right;
    margin-top: 4px;
  }
  &__tabs{}
  &__tab{
    width: 100%;
    margin-bottom: 24px;
    height: 55px;
    display: block;
    li{
      display: block;
      float:left;
      width: 50%;

      &.ui-tabs-active{
        a{
          background: #f37854;
          color: $white;
        }
      }
    }
  }
  &__tab-link{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    font-family: $HelveticaNeueMedium;
    font-size: 16px;
    font-weight: 500;
    color:#f37854;
    background: $white;//
    height: 55px;
    line-height: 54px;
    &:hover, &:active{
      background: #f37854;
      color: $white;
      text-decoration: none;
    }
  }
  @media (max-width:767px) {
    padding-top: 37px;
    padding-bottom: 20px;
    margin-bottom: 33px;
    &__title{
      margin-bottom: 33px;
    }
    &__tabs{
      margin-bottom: 38px;
    }
    &__area{
      margin-bottom: 33px;
    }
  }
  @media (max-width:1199px) {
    &__area{
      margin-bottom: 40px;
    }
    &__img{
      text-align: center;
    }
  }
}
.area-item{
  flex:0 0 50%;
  text-align: center;
  position: relative;
  margin-top: 20px;
  margin-bottom: 11px;

  &:before{
    content: '';
    position: absolute;
    width: 1px;
    height: 83%;
    background: $white;
    right: 0;
    top:0;
  }
  &:last-child{
    &:before{
      opacity: 0;
    }
  }
}
.area-title{
  font-family: $HelveticaNeueMedium;
  font-size: 12px;
  line-height: 1.2;
  text-transform: uppercase;
  margin-bottom: 12px;
}
.area-number{
  font-size: 35px;
  line-height: 35px;
  span{
    font-size: 16px;
    sup{
      font-size: 10px;
    }
  }
  @media (max-width:767px) {

  }
}
.plaining-figure{
  margin: 0;
  display: inline-block;
  text-align: right;
}
.plaining-figcaption{
  text-align: center;
  font-family: $HelveticaNeueMedium;
  font-weight: 500;
  &__img{
    margin-bottom: 2px;
  }
  &__name{
    font-size: 16px;
    line-height: 18px;
    color: $black;
    margin-bottom: 2px;
  }
  &__click{
    font-size: 10px;
    line-height: 18px;
    color: #ff7951;
    text-transform: uppercase;
    letter-spacing: 2px;
  }
}

.hd-construction-progress{
  &__title{
    margin-bottom: 49px;
  }
  &__items{
    margin-bottom: 10px;
  }
  @media (max-width:991px) {
    &__items{
      margin-bottom: 40px;
      .row{
        padding-bottom: 15px;
      }
    }
  }
  @media (max-width:767px) {
    &__title{
      margin-bottom: 33px;
    }
    &__items{
      .row{
        padding-bottom: 5px;
      }
    }

  }
}
