//app form
.app-form{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  &--orange{
    background: $orange;
  }
  .app-form__fieldset{
    width:calc((100% - 60px) / 3);
    margin-bottom: 20px;
    & + .app-form__fieldset{
      margin-left: 30px;
    }
  }
  input, textarea{
    width: 100%;
    height: 50px;
    padding-left: 0px;
    font-family: $Roboto;
    font-size: 16px;
    color: #564f4f;
    background:$white;
    border-bottom: 2px solid #ada6a6;
  }
  .app-form__fieldset-textarea{
    width: 100%;
    margin-left: 0 !important;
    margin-top: 16px;
    margin-bottom: 26px;
    textarea{
      width: 100%;
      height: 38px;
      background: transparent;
    }
  }
  .app-form__fieldset-submit{
    width: 180px;
    margin-bottom: 0px;
    margin-left: 20px ;

    &:hover{
      input{
        cursor:pointer;
      }
    }
  }

  .app-form__submit{
    width: 100%;
    background: $orange;
    border: 2px solid $orange;
    transition: all 0.5s ease;
    height: 42px;
    line-height: 39px;
    &:hover{
      background: transparent;
    }
    &.btn--border-red{
      background: transparent;
      color: $orange;
      &:hover{
        background: $orange;
        color: $white;
      }
    }
  }
  .app-form__personal{
    font-family: $Roboto;
    font-weight: 300;
    color:#bdb9b9;
    font-size: 14px;
    line-height: 1.2;

    .app-form__personal-link{
      display: inline-block;
      color:#bdb9b9;
    }
    .label-checkbox{
      width: 100%;

    }
  }
  .app-form__itogo{
    font-size: 36px;
    line-height: 1.2;
    color: $grey;
    font-weight: bold;
    sup{
      font-size: 22px;
    }
  }
  .app-form__links{
    max-width: 385px;
    width: 100%;
    display: inline-flex;
    justify-content: space-between;
    .app-form__submit{
      width: 180px;
    }
  }


  @media (min-width:768px) and (max-width:991px) {
    .app-form__personal{
      width: calc(100% - 200px);
    }
  }
  @media (max-width:767px) {
    .app-form__fieldset{
      width: 100%;
      margin-left: 0 !important;
      margin-bottom: 16px;
    }
    .app-form__fieldset-textarea{
      margin-top: 11px;
    }
    .app-form__fieldset-submit{
      width: 240px;
      margin-left: auto !important;
      margin-right: auto;
      margin-top: 20px;
    }
  }

}

.app-form--bron{
  .app-form__fieldset{
    width:calc((100% - 60px) / 3);
    margin-bottom: 20px;
    & + .app-form__fieldset{
      margin-left: 0px;
    }
    &:nth-child(4), &:nth-child(7){
      margin-left: 0px;
    }
  }
  .app-form__fieldset-select{
    border-bottom: 2px solid #ada6a6;
    [data-yam-select] .value{
      height: 50px;
    }
    .value{
      padding: 0;
      font-family: $Roboto;
      font-size: 16px;
      color: #564f4f;
      text-transform: none;
      &:after{
        right: 0;
        opacity: 0.6;
      }
    }
    .select{
      padding-right: 4px;
      left: -20px;
      li{
        font-size: 16px;
        color: #564f4f;
        text-transform: none;
      }
    }
  }
  .app-form__fieldset-textarea{
    width: 100%;
    margin-bottom: 37px;
  }
  .app-form__fieldset-date, .app-form__fieldset-quantity{
    width:calc((100% - 90px) / 4);
  }
  .app-form__fieldset-date{
    position: relative;
    img{
      position: absolute;
      z-index: 2;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
    }
  }

  @media (max-width:767px) {
    .app-form__fieldset{
      width: 100%;
    }
    .app-form__fieldset-date{
      width: 100%;
    }
    .app-form__fieldset-quantity{
      width: calc((100% - 30px)/2);
    }
    .app-form__itogo{
      width: 100%;
      text-align: center;
      margin-bottom: 33px;
      font-size: 28px;
      sup{
        font-size: 18px;
      }
    }
    .app-form__links{
      margin-left: auto;
      margin-right: auto;
      .btn{
        width: 48%;
        max-width: 180px;
      }
    }
  }
}



// wp
.wpcf7-form-control-wrap{
  display: block;
}
span.wpcf7-not-valid-tip{
  display: block !important;
  width: 100%;
  position: absolute;
  top:8px;
  font-size: 16px;
  font-weight: normal;
  color: $orange;
}
.app-form__input.wpcf7-not-valid{
  border: 0;
  border-bottom: 2px solid $orange;
}
