.history-house-gallery{
  width: 1170px;
  &__item{
    width: calc(100% - 400px);
    margin-bottom: 30px;
    &.bl-370{
      width: 370px;

    }
    &.h50{
      .history-house-gallery__item-link{
        &:first-child{
          margin-bottom: 30px;
        }
      }
    }
    &.ml-30{
      margin-left: 30px;
    }
    &.mr-30{
      margin-right: 30px;
    }
  }
  &__item-link {
    display: block;
    position: relative;
    transition: all 0.5s ease;
    &:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background: linear-gradient(to top, rgba(0, 0, 0, 0.8), transparent);
      background-size: 100% auto;
      z-index: 1;
    }
    .img {

    }
    .title {
      position: absolute;
      left: 20px;
      bottom: 14px;
      z-index: 1;
      &__h2 {
        font-family: $HelveticaNeueThin;
        font-weight: 100;
        font-size: 24px;
        line-height: 1.2;
        color: $white;
        margin-bottom: 0;
      }
      &__h3 {
        font-family: $HelveticaNeueLight;
        font-weight: 300;
        font-size: 16px;
        line-height: 24px;
        color: #ddd;
        margin: 10px 0 0;
      }
    }
    &:hover {
      .title {
        &__h2 {
          color: #f28058;
        }
      }
    }
  }
  @media (max-width:1199px) {
    width: 100%;
    &__item{
      height: 100%;
      .img{
        height: 100%;
        img{
          object-fit: cover;
          height: 100%;
        }
      }
    }
  }
  @media (max-width:991px) {
    width: 100%;
    &__item-link{
      .title{
        &__h2{
          font-size: 30px;
          line-height: 1.2;
        }
        &__h3{
          margin: 0;
        }
      }
    }
  }
  @media (min-width: 768px) and (max-width: 991px) {
    &__item{
      width: 49.3%;
      overflow: hidden;
      margin-bottom: 10px;
      &.bl-370{
        width: 49.3%;

      }
      &.ml-30{
        margin-left: 5px;
      }
      &.mr-30{
        margin-right: 5px;
      }
      &.h50{
        .history-house-gallery__item-link{
          &:first-child{
            margin-bottom: 10px;
          }
        }
      }
      .img{
        max-height: 480px;
      }
    }
  }
  @media (max-width:767px) {
    width: 100%;
    &__item{
      width: 100%;
      max-width: 370px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 20px;
      &.ml-30{
        margin-left: auto;
      }
      &.mr-30{
        margin-right: auto;
      }
      .img{
        height: auto;
        img{
          width: 100%;
          object-fit: cover;
          height: 213px;
        }
      }
    }
    &__item-link{
      .title{
        left: 20px;
        bottom: 14px;

        &__h3{
          display: none;
        }
      }
      &.h50{
        .history-house-gallery__item-link{
          &:first-child{
            margin-bottom: 20px;
          }
        }
      }

    }

  }
}

