.btn{
  display: inline-block;
  font-family:$HelveticaNeueMedium;
  font-size: 10px;
  font-weight: 500;
  color:$white;
  text-transform: uppercase;
  background: $orange;
  width: 200px;
  height: 50px;
  line-height: 49px;
  letter-spacing: 2px;
  text-align: center;
  border: 1px solid $orange;
  transition: all 0.5s ease;
  position: relative;
  cursor: pointer;
  span{
    position: relative;
    z-index: 2;
  }
  &:before{
    content: "";
    position: absolute;
  }
  &:after{
    content: "";
    position: absolute;
  }
  &:hover{
    background: transparent;
    color: $orange;
  }
  &--border-orange{
    background: transparent;
    border: 1px solid $orange;
   /* &:before{
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background: $orange;
      transition: all 0.3s ease;

      opacity: 0;
    }*/
    &:hover{
      color:$white;
      background: $orange;
      /*&:before{
        opacity: 1;
      }*/
    }
  }
  &--w220{
    width: 220px;
  }
  &--w230{
    width: 230px;
  }
  &--w250{
    width: 250px;
  }
  &--w260{
    width: 260px;
  }
  &--w280{
    width: 280px;
  }
}
@-webkit-keyframes videomodule-anim {
  0% {
    -webkit-transform: scale(0.68);
    transform: scale(0.68); }
  100% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    opacity: 0; }
}

@keyframes videomodule-anim {
  0% {
    -webkit-transform: scale(0.68);
    transform: scale(0.68); }
  100% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    opacity: 0; }
}
.video-module{
  position: relative;
  display: block;
}
.btn-play {
  position: relative;
  z-index: 2;
  left: 0;
  right: 0;
  top:0;
  bottom: 0;
  margin: auto;
  background-color: #ff815c;
  border-color: #ff815c;
  width: 37px;
  height: 37px;
  -webkit-border-radius: 50px;
  border-radius: 50px;
  text-align: center;
  line-height: 37px;
  font-size: 27px;
  cursor: pointer;
  display: inline-block;
  -webkit-transition: .15s ease-in-out;
  -o-transition: .15s ease-in-out;
  transition: .15s ease-in-out;
  -webkit-transition-property: color, background, border-color;
  -o-transition-property: color, background, border-color;
  transition-property: color, background, border-color;
  &:before{
    -webkit-animation-delay: .5s !important;
    animation-delay: .5s !important;
    border-color: #ff815c;


  }

  &:after, &:before{
    content: '';
    border: 1px solid;
    width: 150%;
    height: 150%;
    border-color: #ff815c;
    -webkit-border-radius: 50px;
    border-radius: 50px;
    position: absolute;
    left: -25%;
    top: -25%;
    opacity: 1;
    -webkit-animation: 1s videomodule-anim linear infinite;
    animation: 1s videomodule-anim linear infinite;
  }
  img{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
  }
}

.video-module .btn-play.shadow {
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.05);
}


.pagination{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 60px;
  &__link{
    padding: 0 22px;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 18px;
    color:#aeaeae;
    &.prev{
      width: 18px;
      height: 17px;
      background: url("../images/arrow-h17.png") no-repeat center;
    }
    &.next{
      width: 18px;
      height: 17px;
      background: url("../images/arrow-h17.png") no-repeat center;
      transform: rotate(180deg);
    }
    &.current{
      color:$orange;
    }
    &:hover{
      color:$black;
    }
  }
  @media (max-width: 767px) {
    margin-top: 40px;
    &__link{
      padding: 0 16px;
    }
  }
}
.breadcrumbs{
  background: #f6f6f6;
  padding-top: 12px;
  padding-bottom: 14px;

  width: 100%;
  position: fixed;
  z-index: 100;
  top:72px;
  .container{
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: center;
  }
  &__prev{
    display: inline-block;
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 12px;
    color:$orange;
    padding-right: 30px;
    padding-left: 16px;
    position: relative;
    margin-right: 26px;
    &:before{
      content: "";
      position: absolute;
      left: 0;
      top:3px;
      width: 5px;
      height: 9px;
      background: url("../images/arrow-h10.png") no-repeat center;
      transform: rotate(180deg);
    }
    &:after{
      content: "|";
      position: absolute;
      font-family: $HelveticaNeueRegular;
      font-weight: normal;
      font-size: 12px;
      color:#aeaeae;
      right: 0;
    }
    span{
      position: relative;
      &:before{
        content: "";
        position: absolute;
        width: 0;
        height: 1px;
        background: $orange;
        bottom: 0;
        left: 0;
        transition: width 0.3s linear;
      }
    }
    &:hover{
      color:$orange;
      span{
        &:before{
          width: 100%;
        }
      }
    }
  }
  &__list{
    display: inline-block;
  }
  &__link{
    display: inline-block;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 12px;
    color:#aeaeae;
    position: relative;
    padding-right: 30px;
    &:before{
      content: "";
      position: absolute;
      width: 6px;
      height: 1px;
      background: #aeaeae;
      right: 10px;
      top:0;
      bottom: 0;
      margin: auto;
    }

    span{
      position: relative;
      &:before{
        content: "";
        position: absolute;
        width: 0;
        height: 1px;
        background: #aeaeae;
        bottom: 0;
        left: 0;
        transition: width 0.3s linear;
      }
    }
    &:last-child{
      padding-right: 0;
      &:before{
        opacity: 0;
      }
    }
    &:hover{
      color:#aeaeae;
      span{
        &:before{
          width: 100%;
        }
      }
    }
  }
  @media (max-width:767px) {
    position: relative;
    top: 0;
    margin-bottom: 32px;
    &__prev{
      padding-right: 20px;
      margin-right: 16px;
    }
    &__link{
      padding-right: 16px;
      &:before{
       right: 3px;
      }

    }
  }
}
///// link-detail content toggle
.link-detail{
  display: none;
  &__link{
    text-align: center;
    position: relative;
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 10px;
    text-transform: uppercase;
    letter-spacing: 2px;
    color:#ff8156;

    span{
      position: relative;
      padding-left: 34px;
      &:before{
        content: "";
        position: absolute;
        width: 25px;
        height: 25px;
        left: 0;
      }
    }

  }
  .down{
    display: block;
    span{
      &:before{
        background: url("../images/icon-circlePlus.png") no-repeat center;
      }
    }
  }
  .up{
    display: none;
    span{
      &:before{
        background: url("../images/icon-circleMinus.png") no-repeat center;
      }
    }
  }
  &.active{

    .down{
      display: none;
    }
    .up{
      display: block;
    }
  }
  @media (max-width:1199px) {
    display: block;
  }
}


