$selectHeight: 45px;

// native select-options styles
select {
  border: 1px solid #f6f6f6;
  font-size: 18px;
  color: #C4C4C4;
  height: $selectHeight;
  cursor: pointer;
  outline: none;
  padding: 0 5px;
  margin: 0 0 10px;

  option {
    border: none;
    outline: none;
    font-size: 18px;
    color: #C4C4C4;
    width: 100%;
  }
}

//select styles

[data-kr-select] {
  position: relative;
  opacity: 0;
  height: $selectHeight;
  //display: block;
  margin: 0 0 0px;

  &.show {
    opacity: 1;
    transition: all 0.3s ease;
    height: auto;
  }

  &.active {
    .value:after {
      transform: rotate(180deg);
      transition: all 0.3s ease;
    }
  }
  .value {
    background: $white;
    height: 50px;
    line-height: 49px;
    padding: 0 20px;
    display: block;

    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color:$black;
    //text-transform: uppercase;
    box-sizing: border-box;
    border: 1px solid #f6f6f6;
    border-radius: 0px;

    //transition: all .5s ease;
    position: relative;



    &:after {
      content: "";
      position: absolute;
      width: 20px;
      height: 13px;
      background: url("../images/select-gal.png") no-repeat center;
      right: 20px;
      top:16px;
      opacity: 1;
    }
  }


  .select {
    display: none;
    margin: 0;
    list-style-type: none;
    position: absolute;
    top: 42px;
    left: 0;
    right: 0;
    z-index: 100000;
    background: $white;//f7f8fa
    border-top: none;
    border-left: 1px solid #f6f6f6;
    border-right: 1px solid #f6f6f6;
    border-bottom: 1px solid #f6f6f6;
    border-radius:0;
    padding: 0px 0px 0px 0px;
    box-sizing: content-box;
    overflow: hidden !important;
    box-shadow: 0px 8px 18px 0px rgba(0, 0, 0, 0.09);
    &::-webkit-scrollbar-track{
      -webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);
      background-color: #f6f6f6;
    }

    li{
      width:100%;
      text-align: left;
      font-family: $HelveticaNeueMedium;
      font-weight: 500;
      font-size: 14px;
      color:$black;
      padding: 12px 23px 11px 20px;
      //text-transform: uppercase;
      &:hover{

        cursor: pointer;
        color:$orange;
      }
      &:first-child{
        //display: none;
      }
    }
  }

  @media (max-width:991px) {
    .value{
      font-size: 15px;
    }
    .select{
      font-size: 15px;
    }
  }

}


