.header-services{
  margin-top: 0;
  padding-top: 110px;
  height: 573px;
  font-style: normal;
  text-align: center;
  margin-bottom: 0px;
  color:$white;
  .header-content{
    //padding-top: 129px;
  }
  h2{
    color:$white;
  }
  h3{
    font-family: $HelveticaNeueRegular;
    font-size: 18px;
    color:$white;
    font-style: normal;
    width: 100%;
    margin-bottom: 7px;
  }
  @media (min-width:992px) {
    .header-content{
      padding: 0 50px;
    }
  }
  @media (max-width:767px) {
    height: auto;
   // min-height: 380px;
    padding: 80px 0;
    h2{
      font-family: $HelveticaNeueThin;
    }
  }
}
.services-project{
  margin-top: -182px;
  @media (max-width:767px) {
    margin-top: 0;
  }
}
.services-catalog{
  &__title{
    margin-bottom: 46px;
  }
  &__cart{
    margin-bottom: 30px;
  }
  @media (max-width:767px) {
    &__title{
      margin-bottom: 33px;
    }
    &__cart{
      margin-bottom: 10px;
    }
  }
}
