.not-found{
  text-align: center;
  padding-top: 75px;
  padding-bottom: 20px;
  &__foto{
    margin-bottom: 27px;
    img, svg{
      max-height: 220px;
    }
  }
  &__title{
    margin-bottom: 11px;
  }
  &__desc{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: $black;
    margin-bottom: 29px;
  }
  &__links{
    margin-bottom: 10px;

  }
  &__link{
    display: inline-block;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 14px;
    color:$orange;
    padding: 0 12px;
    margin-bottom: 30px;
    span{
      position: relative;
      &:before{
        content: "";
        position: absolute;
        width: 0;
        height: 1px;
        background: $orange;
        bottom: 0;
        left: 0;
        transition: width 0.3s ease;
      }
    }
    sup{
      font-size: 10px;
      top: -0.4em;
    }
    &:hover{
      color:$orange;
      span{
        &:before{
          width: 100%;
        }
      }
    }
  }
  @media (max-width:767px) {
    padding-top: 35px;
    padding-bottom: 0;
    &__foto{
      margin-bottom: 33px;
      img, svg{
        max-height: 100px;
      }
    }
    &__title{
      margin-bottom: 18px;
    }
    &__desc{
      margin-bottom: 22px;
    }
  }
}
