.counter-items{
  position: relative;
  .ctrl{
    position: absolute;
    width: 21px;
    height: 21px;
    top:0;
    bottom: 0;
    margin: auto;
    cursor: pointer;
    &.minus{
      left: 0;
      &:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 2px;
        background: $orange;
        top:0;
        left: 0;
        bottom: 0;
        margin: auto;
      }
    }
    &.plus{
      right: 0;
      &:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 2px;
        background: $orange;
        top:0;
        right: 0;
        bottom: 0;
        margin: auto;
      }
      &:after{
        content: "";
        position: absolute;
        height: 100%;
        width: 2px;
        background: $orange;
        top:0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
      }
    }
  }
  .counter{
    text-align: center;
  }
}
