.sl-white{

  .slick-dots{
    position: absolute;
    bottom: 26px;
    li{
      pointer-events: auto;
      height: 12px;
      width: 12px;
      margin: 0 5px;

      button {
        bottom: 0;
        height: 12px;
        width: 12px;
        border-radius: 50%;
        background: transparent;
        border:0;
        padding: 0;
        opacity: 1;
        &:before{
          content: '';
          height: 12px;
          width: 12px;
          left: 0;
          right: 0;
          top:0;
          bottom: 0;
          margin: auto;
          border-radius: 50%;
          background: transparent;
          border:1px solid rgba($white, 0.5);
          opacity: 1;
        }


      }
      &.slick-active {
        button {
          &:before {
            background: transparent;
            width: 12px;
            height: 12px;
            border:2px solid  rgba($white,1);


          }

        }

      }
      &:hover{
        button{
          background: transparent;
          &:before, &:after {
            background: transparent;
            width: 12px;
            height: 12px;
            background: transparent;
          }
        }

        /*button {
          &:before {
            width: 12px;
            height: 12px;
            border: 2px solid green;

            background: $white;
            //background: url("../images/page-about/before-slider.png") no-repeat top center;
          }
