              transform 0.4s linear 0s,
              border-left-width 0s linear 0.35s; // Solid edge post-rotation
          }
          &:after{

          }
        }*/
      }
    }
  }
  .slick-arrow{
    position: absolute;
    top: 0;
    bottom: 0;

    margin: auto;
    width: 32px;
    height: 103px;
    z-index: 2;
    opacity: 0.4;
    cursor: pointer;
    &.prevArrow{
      left: 40px;
      background: url("../images/sl-arrowH57.png") no-repeat;
      transform: rotate(180deg);
      &:hover{
        opacity: 1;
      }
    }
    &.nextArrow{
      right: 40px;
      background: url("../images/sl-arrowH57.png") no-repeat;
      &:hover{
        opacity: 1;
      }
    }
  }

  @media (max-width:767px) {
    .slick-dots{
      li{
        //width: 21px;
      }
    }
  }
}
.sl-arrow-orange{
  .slick-dots{
    position: absolute;
    bottom: 0px;
    left: 0;
    right:0;
    li{
      pointer-events: auto;
      height: 8px;
      width: 8px;
      margin: 0 5px;

      button {
        bottom: 0;
        height: 8px;
        width: 8px;
        border-radius: 50%;
        background: transparent;
        border:0;
        padding: 0;
        opacity: 1;
        &:before{
          content: '';
          height: 8px;
          width: 8px;
          left: 0;
          right: 0;
          top:0;
          bottom: 0;
          margin: auto;
          border-radius: 50%;
          background: #f0f0f0;
          border:0px solid rgba($white, 0.5);
          opacity: 1;
        }

      }
      &.slick-active {
        button {
          &:after{

          }
          &:before {
            width: 8px;
            height: 8px;
            background: #ff815c;
          }
        }

      }
      &:hover{
        button {
          &:before {
            width: 8px;
            height: 8px;
            background: #ff815c;
          }
          &:after{

          }
        }
      }
    }
  }
}
