//standart
//+
html, body{
  max-width: 100%;
  overflow-x: hidden !important;
}
body{
  color:$black;
  font-family: $HelveticaNeueRegular;
  font-weight: normal;
  font-size: 14px;
  line-height: 1.2;
  max-width: 100%;
  overflow-x: hidden !important;
  background: $white;
}

*{
  outline: none;
  &:focus, &:hover, &:active{
    outline: none;
  }
}
.btn.focus, .btn:focus{
  box-shadow: none;
}

input, textarea{
  outline: none;
  border:none;
  border-radius: 0;
}
label{
  margin-bottom: 0;
}
ul{
  padding: 0;
  margin: 0;
  li{
    list-style: none;
  }
}
p{margin-bottom: 0}
a{
  //display: block;
  &:hover, &:focus{
    outline: none;
    text-decoration: none;
  }
}
img{
  max-width: 100%;
}
.bg-gray{
  background: #f6f4f5;
}
.bg-orange{
  background: #f37854;
}

.clear{
  clear:both;
}
.heading-title{
  margin-bottom: 39px;
  @media (max-width:991px) {
    margin-bottom: 33px;
  }
}
//h home s1-s5
h1, .h1{
  font-family: $HelveticaNeueThin;
  font-style: normal;
  font-weight: 100;
  font-size: 100px;
  line-height: 90px;
  text-align: center;
  color:$white;
  @media (min-width:768px) and (max-width: 991px) {
    font-size: 80px;
    line-height: 70px;
  }
}
h2, .h2{
  font-family: $HelveticaNeueThin;
  font-size: 45px;
  line-height: 1.2;
  font-style: normal;
  font-weight: 100;
  color:$black;
  text-transform: none;
  &.white{
    color:$white;
  }
  @media (min-width:768px) and (max-width: 1199px) {
    font-size: 36px;
  }
  @media (max-width:767px) {
    font-family: $HelveticaNeueLight;
    font-weight: 300;
    font-size: 29px;
  }
}
h3, .h3{
  font-family:$HelveticaNeueLight;
  font-size: 36px;
  line-height: 1.2;
  margin-bottom: 12px;
  font-style: normal;
  font-weight: 300;
}

h4, .h4{
  font-family: $HelveticaNeueLight;
  font-size: 24px;
  line-height: 1.2;
  font-style: normal;
  font-weight: 300;
  display: inline-block;
  position: relative;
}

.main{
  margin-top: 144px;
  margin-bottom: 60px;
  &.main-foto{
    margin-top: 114px;
  }
  &.main-project{
    margin-top: 154px;
  }
  &.main-product{
    margin-top: 134px;
  }
  @media (max-width:767px) {
    margin-bottom: 40px;
    margin-top: 72px;
    &.main-foto{
      margin-top: 72px;
      .breadcrumbs{
        margin-bottom: 0;
      }
    }
    &.main-project{
      margin-top: 72px;
      .breadcrumbs{
        margin-bottom: 0;
      }
    }
    &.main-product{
      margin-top: 72px;
      .breadcrumbs{
        margin-bottom: 0px;
      }
    }
  }
}

.line-grey{
  width: 100%;
  height: 1px;
  background: #f6f6f6;
  margin-bottom: 40px;
}
.content{
  font-family: $HelveticaNeueRegular;
  font-weight: normal;
  font-size: 16px;
  line-height: 30px;
  h2, .h2{
    margin-bottom: 31px;
  }
  p{
    margin-bottom: 31px;
  }
  b, strong{
    font-family: $HelveticaNeueBold;
    font-weight: bold;
  }
  @media (max-width:767px) {
    line-height: 26px;
    h2, .h2{
      margin-bottom: 22px;
    }
    p{
      margin-bottom: 28px;
    }
  }
}
