.modal-popup{
  max-width: 700px;
  background: #f6f6f6;
  padding: 53px 65px 62px;
  display: none;
  &__title{
    font-family: $HelveticaNeueLight;
    font-weight: 300;
    font-size: 33px;
    color:$black;
    text-align: center;
    margin-bottom: 16px;
  }
  &__subtitle{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    color:$black;
    text-align: center;
    margin-bottom: 33px;
  }
  ::-webkit-input-placeholder {
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color: $black;
  }
  ::-moz-placeholder         {
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color: $black;
  }/* Firefox 19+ */
  :-moz-placeholder           {
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color: $black;
  }/* Firefox 18- */
  :-ms-input-placeholder      {
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color: $black;
  }
  @media (max-width:767px) {
    padding: 35px 20px 42px;
    &__title{
      font-size: 24px;
    }
  }
}
.modal-popup-form{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  &__title{
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 12px;
    color:#afafaf;
    margin-bottom: 10px;
    span{
      color:#da1414;
    }
  }
  &__item{
    width: calc((100% - 30px)/3);
    margin-bottom: 18px;
    input{
      width: 100%;
      height: 40px;
      font-family: $HelveticaNeueMedium;
      font-weight: 500;
      font-size: 14px;
      color: $black;
      padding: 0 10px 0 20px;
    }
  }

  &__textarea{
    width: 100%;
    margin-bottom: 18px;
  }
  textarea{
    width: 100%;
    height: 145px;
    padding: 20px 10px 0 20px;
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color: $black;
  }
  &__personal{
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 11px;
    color:#303030;
    width: 290px;
    a{
      color:$orange;
      text-decoration: underline;
      &:hover{
        text-decoration: none;
      }
    }
  }
  &__submit{}
  @media (max-width:767px) {
    &__item{
      width: 100%;
    }
    &__personal{
      width: 100%;
      text-align: center;
      margin-bottom: 18px;
    }
    &__submit{
      margin-left: auto;
      margin-right: auto;
    }
  }
}
.modal-calculate-list{
  border-top:1px solid #e6e6e6;
  border-bottom:1px solid #e6e6e6;
  padding: 40px 0 17px;
  margin-bottom: 37px;
  margin-top: 4px;
  &__title{
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 10px;
    color:#b0b0b0;
    text-transform: uppercase;
    margin-bottom: 33px;
  }
  .list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    li{
      position: relative;
      padding-left: 24px;
      font-family: $HelveticaNeueMedium;
      font-weight: 500;
      font-size: 15px;
      width: 47.29%;
      margin-bottom: 20px;
      &:before{
        content: "";
        position: absolute;
        width: 4px;
        height: 4px;
        background: $orange;
        border-radius: 50%;
        left: 0;
        top:7px;

      }
    }
  }
  @media (max-width:413px) {
    .list{
      li{
        width: 100%;
      }
    }
  }
}

#modal-thank{
  .modal-popup__subtitle{
    max-width: 384px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 0;
  }
  @media (min-width:768px) {
    width: 700px;
  }
}

.br-none768{
  display: block;
  @media (max-width: 991px) {
    display: none;
  }
}

.search-form {
  position: absolute;
  right: 0px;
  top: 0px;
  ::-webkit-input-placeholder {
    font-family: $HelveticaNeueLight;
    font-weight: 300;
    font-size: 14px;
    color: #acacac;
  }
  ::-moz-placeholder         {
    font-family: $HelveticaNeueLight;
    font-weight: 300;
    font-size: 14px;
    color: #acacac;
  }/* Firefox 19+ */
  :-moz-placeholder           {
    font-family: $HelveticaNeueLight;
    font-weight: 300;
    font-size: 14px;
    color: #acacac;
  }/* Firefox 18- */
  :-ms-input-placeholder      {
    font-family: $HelveticaNeueLight;
    font-weight: 300;
    font-size: 14px;
    color: #acacac;
  }
}

.search-field {
  background-color: transparent;
  //background-image: url("../images/lupa-menuWhite.png");
  background-image: url("../images/glass.svg");
  background-position: 5px center;
  background-repeat: no-repeat;
  background-size: 24px 24px;
  border: none;
  cursor: pointer;
  height: 34px;
  margin: 0px 0;
  padding: 0 0 0 34px;
  position: relative;
  -webkit-transition: width 400ms ease, background 400ms ease;
  transition:         width 400ms ease, background 400ms ease;
  width: 0;
  font-family: $HelveticaNeueLight;
  font-weight: 300;
  font-size: 14px;
  color:$black;
}

/*.search-field:focus,*/ .search-field:hover {
  background-color: #fff;
  //border: 2px solid red;
  cursor: text;
  outline: 0;
  width: 230px;
  background-image: url("../images/magnifying-glass-orange.svg");
}

.search-submit {
  display: none;
}

input[type="search"] {
  -webkit-appearance: textfield;
}

.header__search{
  position: relative;
}
.mb0-767{
  @media (max-width: 767px) {
    margin-bottom: 0;
  }
}
