.header-about{
  margin-top: 0;
  height: 380px;
  font-style: normal;
  text-align: center;
  margin-bottom: 52px;
  &__content{
    color:$white;
    .h2{
      color: $white;
    }
  }
  &__title{
    color:$white;
  }
  &__subtitle{
    font-family: $HelveticaNeueRegular;
    font-size: 18px;
    line-height: 1.2;
    font-weight: normal;
    margin-bottom: 8px;
  }
  @media (max-width:767px) {
    margin-bottom: 33px;
  }
}
.header-content{
  color: $white;
}

.about-content{

}

.about-text{
  font-family: $HelveticaNeueRegular;
  font-size: 16px;
  line-height: 30px;
  color: $black;
  margin-bottom: 50px;
  @media (max-width:767px) {
    margin-bottom: 33px;
  }
}

.about-advantages{
  margin-bottom: 74px;
  .about-advantages__item{
    width: calc(100%/3);
    text-align: center;
    padding: 0 35px;
    .about-advantages__icon{
      height: 65px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 14px;
      img{
        display: block;
      }
    }

    .about-advantages__text{
      font-family: $HelveticaNeueMedium;
      font-size: 16px;
      line-height: 24px;
    }
  }
  @media (max-width:991px) {
    margin-bottom: 57px !important;
    padding-bottom: 43px;
  }
}

.about-blockquote{
  font-family:$HelveticaNeueLight ;
  border-top: 1px solid #f37854;
  border-bottom: 1px solid #f37854;
  padding: 32px 0 36px;
  position: relative;
  text-align: center;
  font-size: 24px;
  line-height: 36px;
  color: #f37854;
  margin-bottom: 51px;
  &:before{
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top:-16px;
    margin: auto;
    width: 70px;
    height: 30px;
    background: #fff url("../images/before-blockquote.png") no-repeat center;
  }
  @media (min-width:992px) {
    padding: 32px 80px 36px;
  }
  @media (max-width:767px) {
    margin-bottom: 33px;
  }
}
.about-command{
  margin-bottom: 11px;

  .h2{
    text-align: center;
    margin-bottom: 37px;
  }
  @media (min-width:768px) and (max-width:991px) {
    margin-bottom: 35px;
  }
  @media (max-width:767px) {
    margin-bottom: 33px;
    .h2{
      margin-bottom: 34px;
    }
  }
}
.about-command-list{
  text-align: center;
  margin-bottom: 26px;
  display: flex;
  overflow-x: auto;
  li{
    display: inline-block;
    margin: 0 3px 15px;
    &.ui-tabs-active{
      a{
        background: #f37854;
        color: $white;
      }
    }
    a{
      display: inline-block;
      padding: 4px 20px;
      background: transparent;
      border: 1px solid #f37854;
      font-size: 14px;
      color:#909090;
      line-height: 24px;
      transition: all 0.5s ease;
      white-space: nowrap;
      &:hover{
        background: #f37854;
        color: $white;
        text-decoration: none;
      }
    }
  }
  @media (min-width: 992px) and (max-width: 1199px) {
    margin-left: -2px;
    li{
      a{
        padding: 4px 11.5px;
      }
    }
  }
  @media (min-width: 768px) and (max-width: 991px) {
    margin-left: -2px;

  }
  @media (max-width:767px) {
    display: flex;
    overflow-x: auto;
    margin-bottom: 6px;
  }
  @media (min-width:1200px) {
    justify-content: center;
  }
}

.about-command-item{
  box-shadow: 15.556px 15.556px 32px 0px rgba(0, 0, 0, 0.06);
  max-width: 271px;
  margin: 0 auto 40px;

  .about-command-item__foto{

  }
  .about-command-item__text{
    background: $white;
    padding-left: 30px;
    padding-bottom: 25px;
    padding-top: 16px;
  }
  .about-command-item__name{
    font-family: $HelveticaNeueRegular;
    font-size: 24px;
    line-height: 1.2;
    margin-bottom: 16px;

  }
  .about-command-item__position{
    font-family: $HelveticaNeueRegular;
    font-size: 16px;
    line-height: 1.2;
  }
  @media (min-width:768px) and (max-width: 1199px) {
    max-width: 100%;
    box-shadow:0px 15.556px 19px 0 rgba(0,0,0,.06);
    margin: 0 auto 50px;
    .about-command-item__foto{
      img{
        width: 100%;
      }
    }
  }
  @media (max-width:767px) {
    margin: 0 auto 50px;
  }
}
.about-command-items{

}
.about-slider{

  .slick-slide {
    position: relative;
    video {
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      min-width: 100%;
      min-height: 100%;
      width: auto;
      height: auto;
      transform:translate(-50%, -50%);
    }
  }

}

.about-slider__item{
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 493px;
}
