.page-gallery{
  .history-house{
    margin-bottom: 30px ;
  }
  .history-house-gallery{
    & + div{
      margin-top: 30px;
      margin-bottom: 60px;
    }
  }
  @media (min-width:992px) {
    .history-house-gallery{
      &__item-link{
        .title {
          left: 40px;
          bottom: 34px;
          &__h2 {
            font-size: 35px;
          }
        }
      }
    }
  }
  @media (min-width:768px) and (max-width: 991px){
    .history-house-gallery{
      &__item{
        width: calc(100% - 300px);
        margin-bottom: 20px;
        .img{
          height: 490px;
          max-height: 490px;
        }
        &.bl-370{
          width: 280px;
          .img{
            height: 100%;
          }
          .history-house-gallery__item-link{

            margin-bottom: 20px;
            &:last-child{
              margin-bottom: 0;
            }

          }
        }
        &.ml-30{
          margin-left: 0;
        }
        &.mr-30{
          margin-right: 0;
        }
      }
    }
  }
  @media (max-width:767px) {
    .history-house{
      margin-bottom: 40px;
    }
    .history-house-gallery{
      & + div{
        margin-top: 20px;
        margin-bottom: 40px;

      }
    }
  }
}

.heading-title-social{
  .container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  &__link{
    position: relative;
    padding-left: 34px;
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color: $orange;
    &:before{
      content:"";
      position: absolute;
      width: 25px;
      height: 25px;
      background: url("../images/icon-fc.png") no-repeat center;
      background-size: contain;
      left: 0;
      top: 0;
      bottom: 0;
      margin: auto;
    }
    span{
      position: relative;
      &:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        background: $orange;
        bottom: 0;
        right: 0;
        transition: width 0.3s linear;
      }
    }
    &:hover{
      color:$orange;
      span{
        &:before{
          width: 0;
        }
      }
    }
  }
  @media (max-width:767px) {
    &__link{
      display: none;
    }
  }
  @media (min-width:406px) and (max-width:767px){
    .container{
      justify-content: center;
    }
  }
}

.gallery-video{
  padding-top: 49px;
  padding-bottom: 30px;

  &__social{
    display: inline-block;
    position: relative;
    padding-left: 34px;
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color: $orange;

    &:before{
      content:"";
      position: absolute;
      width: 25px;
      height: 25px;
      background: url("../images/icon-fc.png") no-repeat center;
      background-size: contain;
      left: 0;
      top: 0;
      bottom: 0;
      margin: auto;
    }
    span{
      position: relative;
      &:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        background: $orange;
        bottom: 0;
        right: 0;
        transition: width 0.3s linear;
      }
    }
    &:hover{
      color:$orange;
      span{
        &:before{
          width: 0;
        }
      }
    }
  }
  .heading-title{
    margin-bottom: 49px;
  }
  @media (max-width:767px) {
    padding-top: 33px;
    padding-bottom: 45px;
    .heading-title{
      margin-bottom: 33px;
    }
    &__social{
      margin-top: 24px;
    }
  }
}

.gallery-video-item{
  position: relative;
  display: block;
  overflow: hidden;
  margin-bottom: 30px;
  max-width: 370px;
  margin-left: auto;
  margin-right: auto;
  &__foto{
    transition: all 0.3s ease;

  }
  &__info{
    width: 100%;
    padding: 0 20px;
    bottom: 20px;
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  &__title{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color:$white;
    transition: all 0.3s ease;
  }
  &__others{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
  }
  &__icon{
    width: 37px;
    height: 37px;
    border-radius: 50%;
    background: $orange;
    position: relative;
    top:1px;
    transition: all 0.3s ease;
    &:before{
      content: "";
      position: absolute;
      width: 8px;
      height: 10px;
      left: 0;right:0;top:0;bottom:0;
      margin: auto;
      background: url("../images/icon-play.png") no-repeat center;
    }
  }
  &__number{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color:$white;
    margin-left: 11px;
    transition: all 0.3s ease;
  }
  &:hover{
    .gallery-video-item__foto{
      transform: scale(1.05);
    }
    .gallery-video-item__title, .gallery-video-item__number{
      color:$orange;
    }
    .gallery-video-item__icon{
      transform: scale(1.05);
    }

  }
  @media (max-width:767px) {
    margin-bottom: 20px;
    &__title{
      font-size: 16px;
    }
    &__number{
      display: none;
    }
    &__info{
      padding: 0 10px;
      bottom: 13px;
    }
  }
}

