.ui-datepicker{
  table{
    width: 220px;
    background: $white;
  }
  .ui-datepicker-header{
    background: #eeeeee;
    position: relative;
    height: 30px;
    line-height: 29px;
  }
  .ui-corner-all{


    &.ui-datepicker-prev{
      left: 10px;
      position: absolute;
      font-size: 10px;
      color:$orange;
      font-weight: bold;
      cursor: pointer;
    }
    &.ui-datepicker-next{
      right: 10px;
      position: absolute;
      font-size: 10px;
      color:$orange;
      font-weight: bold;
      cursor: pointer;
    }
  }
  .ui-datepicker-title{
    font-size: 12px;
    color: #4c4546;
    font-weight: 600;
    text-align: center;
  }

  .ui-datepicker-calendar{
    padding:16px 18px 10px;
    border-collapse: unset;
    border-spacing: 0;
    th{
      font-size: 10px;
      font-weight: bold;
      color:#a39c9c;
      text-transform: uppercase;
    }
    td{
      font-size: 10px;
      width: 19px;
      height: 19px;
      a,span{
        width: 19px;
        height: 19px;
        background: transparent;
        border-radius: 50%;
        color: $grey;
        display: table-cell;
        vertical-align: middle;
        text-align: center;
        &.ui-state-active{
          color: $white;
          background: $orange;
        }
      }
      &.ui-datepicker-today{
        color: $orange;
        a{
          color: $orange;
          &.ui-state-active{
            color: $white;
          }
        }
      }
    }
  }
}
