
.projects{
  position: relative;
  &:before{
    display: none;
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: 0.8;
    z-index: 3;
  }
  &.active-filter{
    &:before{
      display: block;
    }
  }
  &__filters{
    border-bottom: 1px solid #f6f6f6;
    margin-bottom: 38px;
  }
  &__sorts{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 10px;
    color:#aeaeae;
    text-transform: uppercase;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 38px;

    &.fixed{
      position: fixed;
      top:114px;
      width: 100%;
      background: $white;
      z-index: 100;
      padding-top: 20px;
      padding-bottom: 20px;
      max-width: 1170px;
      @media (min-width:992px) and (max-width: 1199px) {
        max-width: 940px;
      }
    }
  }

  &__sort-title{
    margin-right: 20px;
    display: inline-block;
  }
  &__sort-item{
    position: relative;
    display: inline-block;
    padding: 0 10px;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 10px;
    color:#aeaeae;
    text-transform: uppercase;
    &:after{
      content: "/";
      position: absolute;
      font-family: $HelveticaNeueRegular;
      font-weight: normal;
      font-size: 10px;
      color:#aeaeae;
      right: -2px;
    }
    &.active{
      color: $black;
    }
    &.up, &.down{
      span{
        position: relative;
        padding-right: 10px;
        &:before{
          content: "";
          position: absolute;
          width: 7px;
          height: 8px;
          right: 0;
          top:-1px;
          bottom: 0;
          margin: auto;
          background: url("../images/arrow-smallUpDown.png") no-repeat center;
        }
      }
    }
    &.down{
      span{
        &:before{
          transform: rotate(180deg);
        }
      }
    }
    &:last-child{
      &:after{
        opacity: 0;
      }
    }
    &.active{
      color:$black;
    }
  }
  &__sort{
    span{
      padding: 0 3px;
    }
  }
  &__items{

    .btn{
      margin-top: 30px;
    }
    & + div{
      border-top:1px solid #f6f6f6;
      padding-top: 50px;
      margin-top: 60px;
    }
  }
  @media (max-width:767px) {
    &__items{

      .btn{
        margin-top: 10px;
      }
      & + div{
        margin-top: 40px;
        padding-top: 33px;
      }
    }
    &__sorts{
      margin-bottom: 20px;
      position: relative;
      .sort-items{
        display: none;
        &.active{

          display: block;
          position: absolute;
          background: #fff;
          z-index: 2222;
          width: 100%;
        }
      }
    }
    &__sort{

    }
  }


}

.projects-filter{
  margin-top: 50px;
  padding-bottom: 40px;
  border-bottom:1px solid #f6f6f6;
  margin-bottom: 40px;
  position: relative;
  &__header{
    display: none;

    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    height: 53px;
    position: relative;
    width: 100%;
    &:before{
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      background: #eaeaea;
      bottom: 0;
      left: 0;
    }
    &:after{
      content: "";
      position: absolute;
      width: 1px;
      height: 100%;
      background: #eaeaea;
      left: 50%;
      top: 0;
    }

  }
  &__header-item{
    width: 50%;//
    position: relative;
    font-family: $HelveticaNeueMedium;
    font-weight: normal;
    font-size: 16px;
    color: $black;

    &:before{
      content: "";
      position: absolute;
      width: 9px;
      height: 7px;
      background: url("../images/filter-arrow-black.png") no-repeat center;
      right: 15px;
      top:0;
      bottom: 0;
      margin: auto;
    }
    &:last-child{
      padding-left: 15px;
    }
  }
  &__header-sort{
    &.active{
      &:before{
        transform: rotate(180deg);
      }
    }
  }
  &__top{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    @media (min-width: 768px) and (max-width: 991px) {
      justify-content: flex-start;
    }
  }
  &__items{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    position: relative;
    @media (min-width: 768px) and (max-width: 991px) {
      justify-content: flex-start;
    }
  }
  &__botom{

  }
  &__item{
    &:hover{
      .projects-filter__title{
        color: $orange;
      }
    }
    @media (min-width:768px) and (max-width: 991px) {
      & + .projects-filter__item{
        margin-left: 20px;
      }
      &:nth-child(4){
        margin-left: 0;
      }
      &:nth-last-child(-n+2){
        margin-top: 38px;
      }
    }
  }

  &__title{
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 10px;
    color:#aeaeae;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-bottom: 19px;
  }
  &__price{
    position: relative;
    width: 270px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    height: 50px;
    label{
      position: relative;
      &:after{
        content: "";
        position: absolute;
        right: 0;
        top:2px;
        bottom: 0;
        font-size: 14px;
        font-family: $HelveticaNeueBold;
        font-weight: bold;
      }
      input{
        height: 20px;
        padding: 0 1px;
        color: #000;
        border: none;
        background: 0 0;
        font-size: 14px;
        font-family: $HelveticaNeueBold;
        font-weight: bold;
      }

    }

    .ui-slider{
      margin-top: 17px;
      width: 100%;
      margin-left: 10px;
    }
    .ui-slider {
      position: relative;
    }
    .ui-slider .ui-slider-handle {
      position: absolute;
      z-index: 2;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background: #f28058;
      cursor: pointer;

    }
    .ui-slider .ui-slider-range {
      position: absolute;
      z-index: 1;
      font-size: .7em;
      display: block;
      border: 0;
      overflow: hidden;
    }

    .ui-slider-horizontal {
      height: 2px; /*     */
    }

    .ui-slider-horizontal .ui-slider-handle {
      top: -4px;
      margin-left: -10px;
      &:last-child{
        margin-left: -10px;
      }
    }
    .ui-slider-horizontal .ui-slider-range {
      top: 0;
      height: 100%;
    }
    .ui-slider-horizontal .ui-slider-range-min {
      left: 0;
    }
    .ui-slider-horizontal .ui-slider-range-max {
      right: 0;
    }

    .ui-widget-content {
      background: #dbdcde;
    }

    .ui-widget-header {

      background: #f28058;
    }

    .ui-corner-all {
      -moz-border-radius: 4px;
      -webkit-border-radius: 4px;
      border-radius: 4px;
    }
    @media (min-width: 992px) and (max-width:1199px) {
    width: 200px;
    }
  }
  &__select{
    &.w270{
      width: 270px;
    }
    &.w170{
      width: 170px;
    }
    @media (min-width: 992px) and (max-width:1199px) {
      &.w270{
        width: 200px;
      }
      &.w170{
        width: 160px;
      }
    }
    @media (min-width: 768px) and (max-width:991px) {

      &.w170{
        width: 207px;
      }
    }
  }
  &__floors{}
  &__floor{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: transparent;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 14px;
    color:$orange;
    border: 1px solid $orange;
    &.active{
      background: $orange;
      color:$white;
    }
    & + div{
      margin-left: 17px;
    }
    @media (min-width: 992px) and (max-width:1199px) {
      width: 44px;
      height: 44px;
    }
  }
  &__bottom{
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 10px;
    color:$orange;
    text-transform: uppercase;
    text-align: right;
    margin-top: 20px;
    @media (min-width:768px) and (max-width: 991px) {
      text-align: left;
    }
  }
  &__reset{
    position: relative;
    padding-right: 16px;
    display: inline-block;
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 10px;
    color:$orange;
    text-transform: uppercase;
    cursor: pointer;
    &:before{
      content: "";
      position: absolute;
      width: 9px;
      height: 9px;
      right: 0;
      top:0;
      bottom: 0;
      background: url("../images/close-small.png");
    }
    span{
      position: relative;
      &:before{
        content:"";
        position: absolute;
        width: 100%;
        height: 1px;
        background: $orange;
        left: 0;
        bottom: 0;
      }
    }
    &:hover{
      color: $black;
      span{
        &:before{
          background: $black;
        }
      }

    }
  }
  &__apply{
    position: relative;
    padding-right: 16px;
    display: inline-block;
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 10px;
    color:$orange;
    text-transform: uppercase;
    margin-left: 32px;
    cursor: pointer;
    &:hover{
      color: black;
    }
  }
  &__close{
    display: none;
  }
  &__btns{
    display: none;
    position: relative;
  }
  @media (min-width:768px) and (max-width: 991px) {
    &__item{
      &:first-child{
        width: 47%;
      }
      &:nth-child(2){
        width: 228px;
        .w270{
          width: 100%;
        }
      }
    }

    &__price{
      width: 100%;
    }
    &__floor{
      & + div{
        margin-left: 3px;
      }
    }
    &__bottom{
      position: absolute;
      right: 0;
      bottom: 60px;
    }
    &__apply{
      margin-left: 40px;
    }
  }
  @media (max-width:767px) {
    margin-top: 0;
    padding-bottom: 0;
    border-bottom: 0;
    margin-bottom: 20px;
    &__header{
      display: flex;

    }
    &__close{
      display: block;
      position: relative;
      margin-bottom: 33px;
      .title{
        font-family: $HelveticaNeueThin;
        font-weight: 100;
        font-size: 30px;
        color: $black;
      }
      .close{
        position: absolute;
        right: 0;
        top:0;
        bottom: 0;
        margin: auto;
        width: 22px;
        height: 22px;
        background: url("../images/fancy-close.png") no-repeat center;
      }
    }
    &__btns{
      display: block;
      text-align: center;
      margin-top: 10px;
      margin-bottom: 40px;
      .btn{
        display: block;
        width: 100%;
        max-width: 290px;
        margin: 0 auto 16px;
      }
      .projects-filter__reset{
        text-align: center;
      }
    }
    &__top{
      display: none;
      position: absolute;
      width: 100%;
      top:0;

      background: $white;
      z-index: 5;
      padding-top: 33px;
      &:before{
        content: "";
        position: absolute;
        width: 140%;
        background: $white;
        left: -20%;
        height: 100%;
        top: 0;
      }
      &.active{
        display: block;

      }
    }
    &__bottom{
      display: none;
    }
    &__item{
      width: 100%;
      margin-bottom: 30px;
    }
    &__price{
      width: 100%;
      &.w270{
        width: 100%;
      }
    }
   &__select{
     &.w270{
       width: 100%;
     }
     &.w170{
       width: 100%;
     }
   }

  }
  @media (max-width: 575px) {
    &__header{
      &:before{
        width: 120%;
        left: -10%;
      }
    }
  }
  @media (max-width: 415px){
    &__btns{
      .btn{
        max-width: 100%;
      }
    }
  }
}
.buffer {
  position: absolute;
  top: -1000px;
  left: -1000px;
  visibility: hidden;
  white-space: nowrap;
}
