.compare-items{
  position: relative;
  margin-bottom: 60px;

  @media (max-width:1279px) {
    margin-bottom: 40px;
  }
}

.compare-features-wrapper{
  background: #f8f8f8;
  width:280px;
  position: absolute;
  z-index: 1;
  &:before{
    content: "";
    position: absolute;
    bottom: -64px;
    height: 63px;
    background: #fff;
    width: 100%;
  }
}
.compare-features-columns{
  li{
    font-family: $HelveticaNeueBold;
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    text-transform: uppercase;
    padding: 25px 20px;
    border-top: 1px solid transparent;
    /*&:nth-child(2){
      padding-top: 130px;
      border-top: 1px solid transparent;
    } */
    &:first-child{
      padding-bottom: 78px;

    }
    &:nth-child(2){

    }
    &:last-child{

    }
  }
}
.compare-product-wrapper{
  position: relative;
  padding-left: 280px;
  max-width: 100%;

  &.overflow{
    /*  overflow-x: scroll;*/
  }
}

.compare-product-columns{
  display: flex;
  flex-wrap: nowrap;
  li{
    width: 216px;
    text-align: center;
    border-right: 1px solid #f0f0f0;
    position: relative;
  }
  .top-info{
    display: block;
    text-align: center;
    color:$black;
    height: 120px;
    overflow-y: hidden;
    position: relative;
    img{
      margin: auto;
    }


  }
  .list{
    h4{
      font-family: $HelveticaNeueBold;
      font-weight: bold;
      font-size: 16px;
      line-height: 16px;
      text-align: center;
      text-transform: none;
    }
    li{
      font-size: 16px;
      line-height: 16px;
      text-align: center;
      text-transform: uppercase;
      padding: 25px 20px;
      border-top: 1px solid #f0f0f0;
      height: 67px;
      &:last-child{
        border-bottom: 1px solid #f0f0f0;
      }
    }
  }
  .delete{
    font-size: 12px;
    text-transform: uppercase;
    padding: 25px 33px 25px 0;
    color:#808080;
    position: relative;
    display: inline-block;
    text-align: center;
    &:before{
      content:"";
      position: absolute;
      width: 20px;
      height: 20px;
      background: url("../images/delete-compare.png") no-repeat;
      right: 0;
      top:22px;
    }
  }
}
@media (min-width: 1200px) {
  .compare-features-wrapper{
    &:before{
      display: none;
    }
  }
  .compare-product-columns{
    .delete{
      padding: 0;
      position: absolute;
      right: 10px;
      top:0;
      &:before{
        top:10px;
      }
      span{
        display: none;
      }
    }
  }
}
@media (max-width: 1199px) {
  .compare-features-wrapper{
    width:200px;
  }
  .compare-features-columns{
    li{
      &:first-child{
        //padding-top: 132px;
      }
    }
  }
  .compare-product-wrapper{
    padding-left: 200px;
  }
  .compare-product-columns{

  }
}
@media (max-width: 767px) {
  .compare-features-wrapper{
    width:161px;
  }

  .compare-features-columns{
    li{
      font-size: 10px;
      &:first-child{
        padding-bottom: 30px;
      }
    }
  }
  .compare-product-wrapper{
    padding-left: 162px;
    li{
      width: 130px;
    }
    .top-info{
      height: 72px;
    }
  }

}


