.header-technology{
  min-height: 517px;
  height: auto;
  text-align: center;
  margin-bottom: 86px;
  &__content{
    color:$white;
  }
  &__title{
    font-family: $HelveticaNeueThin;
    font-weight: 100;
    font-size: 52px;
    color:$white;
    margin-bottom: 13px;
  }
  &__subtitle{
    font-family: $HelveticaNeueThin;
    font-weight: 100;
    font-size: 24px;
    color:$white;
    margin-bottom: 35px;
  }
  .home-acquaintance__video{
    margin-bottom: 0;
  }
  @media (max-width:1199px) {
    &__title{
      font-size: 45px;
    }
  }
  @media (max-width:991px) {
    &__title{
      margin-bottom: 30px;
    }

  }
  @media (max-width:767px) {
    margin-bottom: 64px;
    &__title{
      font-size: 34px;
    }
  }
}
.technology-blockquote{

  border-top: 1px solid #f37854;
  border-bottom: 1px solid #f37854;
  padding: 42px 0 41px;
  position: relative;
  text-align: center;
  margin-bottom: 63px;

  &__title{
    font-family: $HelveticaNeueThin;
    font-weight: 100;
    font-size: 35px;
    color:#e38058;
    margin-bottom: 23px;
  }
  &__desc{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 24px;
    line-height: 36px;
    color:$black;
    max-width: 995px;
    margin-left: auto;
    margin-right: auto;
  }
  &:before{
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top:-26px;
    margin: auto;
    width: 107px;
    height: 49px;
    background: #fff url("../images/before-blockquote2.png") no-repeat center;
  }
  @media (max-width:767px) {
    margin-bottom: 41px;
    padding: 42px 0 34px;
    &__title{
      font-size: 30px;
    }
    &__desc{
      font-size: 20px;
      line-height: 28px;
    }
  }
}
.technology-advantages{
  margin-bottom: 60px;
  &__item{
    text-align: center;
    padding-bottom: 20px;
    position: relative;
  }
  &__icon{
    margin-bottom: 20px;
    img, svg{
      height: 54px;
      margin: 0 auto;
    }
  }
  &__title{
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 16px;
    color:$black;
    margin-bottom: 23px;
  }
  &__desc{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 16px;
    color:$black;
    margin-bottom: 34px;
  }
  &__link{

    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 16px;
    color:#e38059;
    span{
      position: relative;
      &:before{
        content:"";
        position: absolute;
        width: 100%;
        height: 1px;
        bottom: 0;
        left: 0;
        background: #e38059;
        transition: width 0.3s ease;
      }
    }
    &:hover{
      span{
        &:before{
          width: 0;
        }
      }
    }
  }
  @media (max-width:991px) {
    &__item{
      margin-bottom: 28px;
    }
  }
  @media (max-width:767px) {
    margin-bottom: 42px;
    &__title{
      margin-bottom: 17px;
    }
    &__desc{
      line-height: 24px;
      margin-bottom: 25px;
    }
  }
}
.technology-differences{
  margin-bottom: 60px;
  &__img{
    img, svg{
      display: block;
      max-height: 100%;
    }
  }
  &__info{}
  &__title{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 24px;
    line-height: 38px;
    color: #f37854;
    margin-bottom: 34px;
  }
  &__list{
    li{
      font-family: $HelveticaNeueRegular;
      font-weight: normal;
      font-size: 16px;
      line-height: 26px;
      color: $black;
      margin-bottom: 29px;
      position: relative;
      padding-left: 42px;
      &:before{
        content:"";
        position: absolute;
        width: 28px;
        height: 24px;
        background: url("../images/technology-differences-list.png") no-repeat center;
        left: 0;
      }
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  @media (max-width:991px) {
    &__img{
      margin-bottom: 32px;
      img, svg{
        max-height: 330px;
      }
    }
  }
  @media (max-width:767px) {
    margin-bottom: 34px;
    &__img{
      margin-bottom: 32px;
      img, svg{
        max-height: 290px;
      }
    }
    &__title{
      line-height: 1.2;
    }
  }
}

.technology-privilege{
  margin-bottom: 60px;
  &__slider{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 50px;
    position: relative;
    margin-bottom: 53px;
    &:before, &:after{
      content: "";
      position: absolute;
      width: 1px;
      height: 100%;
      background: $orange;
      top:0;
    }
    &:before{
      left: 33.18%;
    }
    &:after{
      right: 33.18%;
    }
  }
  &__item{
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    width: 33.333%;
    padding: 0 10px;
    margin-bottom: 57px;
    &:nth-last-child(-n+3){
      margin-bottom: 0;
    }
  }
  &__icon{
    margin-bottom: 16px;
    img, svg{
      height: 55px;
      margin: 0 auto;
    }
  }
  &__title{
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    color:$orange;//#e58059
    margin-bottom: 24px;
  }
  &__desc{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    color:$black;
  }
  &__subtitle{
    font-family: $HelveticaNeueThin;
    font-weight: 100;
    font-size: 35px;
    color:$orange;
    text-align: center;
    margin-bottom: 33px;
    max-width: 832px;
    margin-left: auto;
    margin-right: auto;
  }
  @media (max-width:1199px) {
    &__slider{
      margin-bottom: 53px !important;
      &:before, &:after{
        display: none;
      }
    }
    &__item{
      margin-bottom: 45px;
      &:nth-last-child(-n+3){
        margin-bottom: 45px;
      }
    }
  }
  @media (max-width:991px) {
    &__slider{
      margin-bottom: 53px !important;

    }
    &__item{
      margin-bottom: 45px;
      &:nth-last-child(-n+3){
        margin-bottom: 45px;
      }
    }
  }
  @media (max-width:767px) {
    margin-bottom: 40px;
    &__slider{
      margin-top: 35px;
      margin-bottom: 33px !important;
    }
    &__subtitle{
      font-size: 24px;
    }
  }
}

.technology-history{
  padding-top: 55px;
  margin-bottom: 52px;
  @media (max-width: 767px) {
    padding-top: 33px;
    margin-bottom: 33px;
  }
}


.technology-history-slider-wr{
  position: relative;
  padding-bottom: 69px;
  .swiper-button-prev, .swiper-button-next{
    bottom: 54px;
    top: auto;
    margin-top: 0;
  }
  .swiper-button-prev{
    background: url("../images/arroe-blackH28.png") no-repeat center;
    left: 0px;
    right: auto;
    &:hover{
      background: url("../images/arroe-orangeH28.png") no-repeat center;
    }
  }
  .swiper-button-next{
    background: url("../images/arroe-blackH28.png") no-repeat center;
    right: 0px;
    left: auto;
    transform: rotate(180deg);
    &:hover{
      background: url("../images/arroe-orangeH28.png") no-repeat center;
    }
  }
  @media (max-width:767px) {
    position: relative;
    padding-top: 55px;
    margin-top: 31px;
    padding-bottom: 35px;
    .swiper-button-prev, .swiper-button-next{
      display: none;
    }
  }
}
.technology-history-slider{
  margin-bottom: 60px;
  margin-top: 45px;
  &__item{
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: center;
  }
  &__foto{
    width: 50%;

  }
  &__info{
    width: calc(50% + 70px);
    position: relative;
    margin-left: -70px;
  }
  &__info-number{
    margin-bottom: 24px;
  }
  &__info-desc{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 24px;
    line-height: 40px;
    color:$black;
  }
  @media (max-width:1199px) {
    &__info-number{

      img, svg{
        height: 90px;

      }
    }
    &__info-desc{
      font-size: 22px;
      line-height: 28px;
    }
  }

  @media (max-width:991px) {

    &__item{

    }
    &__foto{
      width: 60%;
    }
    &__info{
      width: 50%;
      margin-left: -10%;
    }
    &__info-number{
      margin-top: -21px;
      img, svg{
        height: 50px;
        margin:auto;
      }
    }
    &__info-desc{
      font-size: 20px;
      line-height: 28px;
    }
  }
  @media (max-width:767px) {
    margin-top: 0;
    margin-bottom: 0;
    &__item{
      flex-wrap: wrap;
      text-align: center;
    }
    &__foto{
      width: 100%;
    }
    &__info{
      width: 100%;
      margin-left: 0;
    }
    &__info-number{
      margin-top: -21px;
      img, svg{
        height: 50px;
        margin:auto;
      }
    }
    &__info-desc{
      font-size: 20px;
      line-height: 28px;
    }
  }
}
.technology-history-slider-nav{
  padding: 0 150px;
  .swiper-slide{
    &:first-child{
      .technology-history-slider-nav__item{
        &:before{
          width: 70%;
          left: -35%;
        }
      }
    }
    &:last-child{
      .technology-history-slider-nav__item{
        &:after{
          width: 70%;
          right: -35%;
        }
      }
    }
    &.swiper-slide-thumb-active{
      .technology-history-slider-nav__item{
        font-family: $HelveticaNeueMedium;
        font-weight: 500;
        color: $orange;
      }
    }
  }
  &__item{
    font-family: $HelveticaNeueLight;
    font-weight: 300;
    font-size: 14px;
    color:#aeaeae;
    text-align: center;
    /*padding-left: 131px;*/
    position: relative;
    //outline: 1px solid red;
    cursor: pointer;
    &:before{
      content: "";
      position: absolute;
      width: 35%;
      height: 1px;
      background: #e3e2e2;
      left: 0;
      top:0;
      bottom: 0;
      margin: auto;
    }
    &:after{
      content: "";
      position: absolute;
      width: 35%;
      height: 1px;
      background: #e3e2e2;
      right: 0;
      top:0;
      bottom: 0;
      margin: auto;
    }
  }
  @media (max-width: 1199px) {
    padding: 0 50px;
    .swiper-slide{
      &:first-child{
        .technology-history-slider-nav__item{
          &:before{
            opacity: 0;
          }
        }
      }
      &:last-child{
        .technology-history-slider-nav__item{
          &:after{
            opacity: 0;
          }
        }
      }
      &.swiper-slide-thumb-active{
        .technology-history-slider-nav__item{
          font-family: $HelveticaNeueMedium;
          font-weight: 500;
          color: $orange;
        }
      }
    }
    &__item{
      &:before, &:after{
        width: 25%;
      }
    }
  }
  @media (max-width: 991px) {
    padding: 0 30px;
    .swiper-slide{
      &:first-child{
        .technology-history-slider-nav__item{
          &:before{
            opacity: 0;
          }
        }
      }
      &:last-child{
        .technology-history-slider-nav__item{
          &:after{
            opacity: 0;
          }
        }
      }
      &.swiper-slide-thumb-active{
        .technology-history-slider-nav__item{
          font-family: $HelveticaNeueMedium;
          font-weight: 500;
          color: $orange;
        }
      }
    }
    &__item{
      &:before, &:after{
        width: 23%;
      }
    }
  }
  @media (max-width: 767px) {
    padding: 0;
    position: absolute;
    top:0;
    width: 112%;//100
    margin-left: -6%;
    .swiper-slide{
      &:first-child{
        .technology-history-slider-nav__item{
          &:before{
           opacity: 0;
          }
        }
      }
      &:last-child{
        .technology-history-slider-nav__item{
          &:after{
           opacity: 0;
          }
        }
      }
      &.swiper-slide-thumb-active{
        .technology-history-slider-nav__item{
          font-family: $HelveticaNeueMedium;
          font-weight: 500;
          color: $orange;
        }
      }
    }
    &__item{
      &:before, &:after{
        width: 14%;
      }
    }
  }
}

.faq{
  padding: 0 0px;
  margin-top: 10px;
  @media (min-width:1200px) {
    .container{
      padding: 0 100px;
    }
  }
  @media (max-width: 1199px) {

  }

}
.faq-items{

  @media (max-width:1199px) {
    padding: 0;
  }
  @media (max-width: 767px) {
    margin-top: 13px;
  }
}
.faq-item{
  border-bottom: 1px solid #e3e2e2;
  padding-bottom: 6px;
  &:last-child{
    margin-bottom: 5px;
  }
  &__top{
    position: relative;
    padding-top: 36px;
    padding-bottom: 29px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;


  }
  &__title{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 24px;
    color:$black;
    width: calc(100% - 100px);
  }
  &__icon{}
  &__text{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: $black;
    padding-bottom: 30px;
    display:none;
    &.active{

    }

  }
  &__others{}
  &__others-item{
    position: relative;
    padding-right: 30px;
    font-family: $HelveticaNeueBold;
    font-weight: bold;
    font-size: 10px;
    color:$orange;
    text-transform: uppercase;
    letter-spacing: 2px;

    &:after{
      content: "";
      position: absolute;
      width: 19px;
      height: 19px;
      right: 0;
      background-size: cover;
      border-radius: 50%;
      top:-3px;

    }
    &.down{
      &:after{
        background: url("../images/icon-circlePlus.png") no-repeat center;
      }

    }
    &.up{
      display: none;
      &:after{
        background: url("../images/icon-circleMinus.png") no-repeat center;
      }
    }
  }
  &.active_block{
    .faq-item__top{
      .down{
        display: none;
      }
      .up{
        display: block;
      }
      &:before{
        transform: rotate(45deg);
      }
    }
  }
  @media (max-width:767px) {
    &__top{
      padding-top: 16px;
      padding-bottom: 10px;
    }
    &__title{
      font-size: 20px;
      width: calc(100% - 40px);
    }
    &__others-item{
      span{
        display: none;
      }
      &:after{
        top: 0;
        bottom: 0;
        margin: auto;
      }
    }
    &__text{
      padding-bottom: 9px;
    }
  }
}

