.technology-cut{
  &__item{
    position: relative;
  }
  &__title{
    position: absolute;
    left: 80px;
    top:51px;
    pointer-events: none;
    &.h2{
      color: $white;
    }
  }
  &__slider{

  }
  &__slider{
    padding: 0 10px 40px;

  }
  &__slider-item{
    margin-top: 12px;
    .list{
      font-family: $HelveticaNeueRegular;
      font-weight: normal;
      font-size: 16px;
      line-height: 30px;
      color: $black;
    }
  }
  @media (min-width:992px) {
    &__slider{
      display: none;
    }
  }
  @media (min-width:768px) and (max-width: 991px) {
    &__slider{
      .slick-dots{
        opacity: 0;
      }
    }
    &__title{
      font-size: 33px !important;
    }
  }
  @media (max-width:991px) {
    &__title{
      left: 20px;
      top:35px;
    }
  }

  @media (max-width:767px) {
    margin-bottom: 40px;
    &__item{
      padding-top: 56px;
    }
    &__title{
      background: url("../images/cut-bg.png") no-repeat center;
      width: 100%;
      left: 0;
      background-size: cover;
      height: 122px;
      top:0px;
      text-align: center;
      padding-top: 20px;
    }
    &__foto{

    }
    img{
      height: 459px;
      object-fit: cover;
      object-position: center;
    }
  }
  @media (max-width:482px) {

    .container{
      max-width: 100%;
      padding-left: 0;
      padding-right: 0;
    }
    &__foto{
      margin-left: -128px;
    }

  }

}
.mapHouse{
  label {
    cursor: pointer;
    width: 31px;
    height: 31px;
    border-radius: 30px;
    position: absolute;
    z-index: 9;
    &:hover .labelIn {
      display: block
    }
  }
  .labelIn {
    position: relative;

    text-align: center;
    padding: 8px 10px;
    width: 170px;
    background: $black;
    margin: 45px 0 0 -65px;
    display: none;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 16px;
    color:$white;
    &:before{
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 7.5px 7px 7.5px;
      border-color: transparent transparent #000000 transparent;
      top:-7px;
      left: 0;
      right: 0;
      margin: auto;
    }
  }


