.page-news{
  .heading-title{
    margin-bottom: 14px;
  }
}

.news{
  &__filter{
    margin-bottom: 38px;
  }
  &__filter-item{
    display: inline-block;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 10px;
    color:#aeaeae;
    text-transform: uppercase;
    position: relative;
    cursor: pointer;
    & + div, & + a{
      padding-left: 23px;
      &:before{
        content: "/";
        position: absolute;
        font-family: $HelveticaNeueRegular;
        font-weight: normal;
        font-size: 10px;
        color:#aeaeae;
        left: 8px;
      }
    }
    &.active{
      color:$black;
    }
  }
  &__items{
    margin-bottom: 12px;
  }
}
.news-item{
  position: relative;
  display: block;
  margin-bottom: 30px;
  overflow: hidden;
  &__foto{
    position: relative;
    transition: all 0.3s ease;
  }
  &__info{
    position: absolute;
    bottom: 40px;
    padding: 0 40px;
  }
  &__date{
    position: absolute;
    top:40px;
    padding: 0 40px;
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 10px;
    color:#b3b3b3;
    text-transform: uppercase;
    letter-spacing: 2px;
  }
  &__title{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color: $white;
    margin-bottom: 18px;
  }
  &__more{
    position: relative;
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 10px;
    color:$orange;
    text-transform: uppercase;
    letter-spacing: 2px;
    padding-left: 40px;
    height: 30px;
    display: flex;
    align-items: center;
    &:before, &:after{
      content: "";
      position: absolute;
      width: 30px;
      height: 30px;
      border-radius: 50%;
    }
    &:before{
      background: url("../images/arrow-h10.png") no-repeat center;
      left: 0;
    }
    &:after{
      border: 1px solid $orange;
      left: 0;
    }
  }
  &:hover{
    .news-item__foto{
      transform: scale(1.05);
    }
  }
  @media (max-width:767px) {
    height: 300px;
    &__foto{
      height: 100%;
      img{
        width: 100%;
        max-height: 100%;
        object-fit: cover;
      }
    }
    &__info{
      padding: 0 20px;
      bottom: 30px;
    }
    &__date{
      top:20px;
      padding: 0 20px;
    }
    &__title{
      font-size: 16px;
      line-height: 1.2;
    }
  }
}
.news-subscription{
  background: #f6f6f6;
  padding: 54px 100px 58px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top:40px;

  &__title{
    width: 252px;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 24px;
    color:$black;
  }
  &__form{
    width: calc(100% - 252px);
  }
  @media (max-width:1199px) {
    padding: 54px 60px 58px;
  }
  @media (max-width:991px) {
    padding: 36px 40px 40px;
    &__title, &__form{
      width: 100%;
    }
    &__title{
      margin-bottom: 27px;
    }
  }
  @media (min-width: 768px) and (max-width: 991px) {
    padding: 36px 20px 40px;
    &__title{
      width: 252px;
      margin-bottom: 0;
    }
    &__form{
      width: calc(100% - 252px);
    }
  }
  @media (max-width:767px){
    padding-left: 10px;
    padding-right: 10px;
    &__title{
      text-align: center;
    }
  }
}
.form-subscription{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  ::-webkit-input-placeholder {
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color: $black;
  }
  ::-moz-placeholder         {
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color: $black;
  }/* Firefox 19+ */
  :-moz-placeholder           {
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color: $black;
  }/* Firefox 18- */
  :-ms-input-placeholder      {
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color: $black;
  }
  &__email{
    width: 65.27%;
    border: 1px solid $white;
    padding: 0 24px;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 14px;
    color: $black;
  }
  &__submit{
    width: 230px;
    cursor: pointer;
  }
  input{
    height: 50px;
    line-height: 49px;

  }
  @media (max-width:1199px) {
    &__email{
      width: 60%;
    }
    &__submit{
      width: 36%;
    }
  }
  @media (min-width: 768px) and (max-width: 991px) {

  }
  @media (max-width:767px) {
    &__email{
      width: 100%;
      margin-bottom: 20px;
    }
    &__submit{
      width: 100%;
      max-width: 100%;
      margin-left: auto;
      margin-right: auto;
    }
  }

}
