
.header-product-slider{
  margin-bottom: 28px;
  margin-top: 20px;
  .slick-arrow{
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 32px;
    //width: 100%;
    //max-width: 1102px;
    height: 103px;
    z-index: 2;
    opacity:1;
    cursor: pointer;
    left: 0;
    right: 0;

    &.nohover{
      pointer-events: none;
    }

    .prevArrow{
      position: absolute;
      left: 0px;
      width: 32px;
      height: 103px;
      background: url("../images/sl-arrowH57.png") no-repeat;
      transform: rotate(180deg);
      background-size:contain;
      &:hover{
        opacity: 1;
        background: url("../images/arrowh103-orange.png") no-repeat;
        background-size:contain;
      }
    }
    .nextArrow{
      position: absolute;
      width: 32px;
      height: 103px;
      right: 0px;
      background: url("../images/sl-arrowH57.png") no-repeat;
      background-size:contain;
      &:hover{
        opacity: 1;
        background: url("../images/arrowh103-orange.png") no-repeat;
        background-size:contain;
      }
    }

  }
  .slick-slide{
    >div{
      margin: 0 10px;
    }
    &.slick-current{
      .header-product-slider__layouts{
        opacity: 1;
      }
      .header-product-slider__item{
        &:hover{
          .header-product-slider__foto{
            //transform: scale(1);
          }
        }
      }
    }
    &.nextSlide{
      .header-product-slider__foto{
        &:before{
          /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+1,ffffff+100&0+1,0.5+32 */
          background: -moz-linear-gradient(left, rgba(255,255,255,0) 1%, rgba(255,255,255,0.5) 32%, rgba(255,255,255,0.5) 100%); /* FF3.6-15 */
          background: -webkit-linear-gradient(left, rgba(255,255,255,0) 1%,rgba(255,255,255,0.5) 32%,rgba(255,255,255,0.5) 100%); /* Chrome10-25,Safari5.1-6 */
          background: linear-gradient(to right, rgba(255,255,255,0) 1%,rgba(255,255,255,0.5) 32%,rgba(255,255,255,0.5) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
          filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#80ffffff',GradientType=1 ); /* IE6-9 */
        }
      }
      &.hover{
        .header-product-slider__foto{
          //transform: scale(1.05);
        }
      }
    }
    &.prevSlide{
      .header-product-slider__foto{
        &:before{
          /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+0,ffffff+99&0.5+68,0+99 */
          background: -moz-linear-gradient(left, rgba(255,255,255,0.5) 0%, rgba(255,255,255,0.5) 68%, rgba(255,255,255,0) 99%); /* FF3.6-15 */
          background: -webkit-linear-gradient(left, rgba(255,255,255,0.5) 0%,rgba(255,255,255,0.5) 68%,rgba(255,255,255,0) 99%); /* Chrome10-25,Safari5.1-6 */
          background: linear-gradient(to right, rgba(255,255,255,0.5) 0%,rgba(255,255,255,0.5) 68%,rgba(255,255,255,0) 99%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
          filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#80ffffff', endColorstr='#00ffffff',GradientType=1 ); /* IE6-9 */
        }
      }
      &.hover{
        .header-product-slider__foto{
          //transform: scale(1.05);
        }
      }
    }
  }

  &__item{
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease;
    &:hover{
      .header-product-slider__foto{
        //transform: scale(1.05);
      }
    }
  }
  &__foto{
    transition: all 0.3s ease;
    position: relative;
    display: block;
    &:before{
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;

    }
  }
  &__layouts{
    position: absolute;
    width: 100%;
    height: 65px;
    background: rgba(0,0,0,0.6);
    bottom: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    opacity: 0;
  }
  &__layout{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 14px;
    color:$white;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    position: relative;
    &:before{
      content: "";
      position: absolute;
      width: 1px;
      height: 100%;
      background: $white;
      opacity: 0;
      left: 33px;
    }
    &-icon{
      transform: rotate(-90deg);

    }
    img, svg{
      height: 38px;
    }
    &-text{
      margin-left: 13px;
    }
    &+.header-product-slider__layout{
      padding-left: 64px;
      &:before{
        opacity: 0.4;
      }
    }
    &:hover{
      color:$orange;
      .header-product-slider__layout-icon{
        transform: rotate(-90deg) scale(1.05);
      }
    }
  }
  @media (min-width:1200px) {
    .slick-arrow{
      &.slick-arrow-item--prev{
        left: 50%;
        right: 50%;
        margin-left: -585px;
      }
      &.slick-arrow-item--next{
        left: 50%;
        right: 50%;
        margin-right: -585px;
      }
    }
  }
  @media (max-width:1199px) {
    .slick-slide{
      > div{
        margin: 0 5px;
      }

    }
    .slick-arrow{
      width: 32px;
      &.slick-arrow-item--prev{
        left: 0;
        right: auto;
      }
      &.slick-arrow-item--next{
        right: 0;
        left: auto;
      }
      .prevArrow{
        left: 15px;

        background-size: contain;
      }
      .nextArrow{
        right: 15px;

        background-size: contain;
      }
    }
  }
  @media (max-width:991px) {
    margin-top: 10px;
  }
  @media (min-width:768px) and (max-width: 991px) {
    &__foto{
      img{
        max-height: 338px;
      }
    }
  }
  @media (max-width: 767px) {
    .slick-arrow{
      height: 69px;
      /*top:22%;
      bottom: auto;*/
      width: 22px;
      left: auto;
      right: auto;

      &.slick-arrow-item--prev{
        left: 0;
      }
      &.slick-arrow-item--next{
        right: 0;
      }
      .prevArrow{
        height: 69px;


      }
      .nextArrow{
        height: 69px;

      }

    }
    .slick-slide{
      &.nextSlide {
        .header-product-slider__foto {
          &:before {
            opacity: 0;
          }
        }
      }
      &.prevSlide {
        .header-product-slider__foto {
          &:before {
          }
        }
      }
    }
    &__foto{
      img{
        height: 238px;
        object-fit: cover;
      }
    }
    &__layouts{
      padding: 0 10px;
    }
    &__layout{

      img, svg{
        height: 30px;
      }
    }
  }
  @media (max-width: 433px){
    .slick-slide{
      > div{
        margin: 0 0px;
      }
    }
    &__layout{
      &:before{
        left: 21px;
      }
      &+.header-product-slider__layout{
        padding-left: 40px;
      }
    }
  }
}

