  }
  &__payment-desc{
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 24px;
    sup, sub{
      font-size: 0.6em;
    }
  }
  &__desc{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
