      }
    }
  }
  &__list{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: $black;
    padding: 0 100px 45px;
    border-bottom: 1px solid #e3e3e3;
    margin-bottom: 45px;
    li{
      padding-left: 22px;
      position: relative;
      &:before{
        content: "";
        position: absolute;
        width: 6px;
        height: 6px;
        background: $orange;
        border-radius: 50%;
        left: 0;
        top:8px;
      }
      p{
        margin-bottom: 26px;
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
  }
  &__dop-order-items{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-bottom: 3px;
    .checkbox{
      width: 25%;
      margin-bottom: 45px;
    }
    .checkbox input {
      position: absolute;
      z-index: -1;
      opacity: 0;
      margin: 10px 0 0 20px;
    }
    .checkbox__text {
      position: relative;
      padding: 0 0 0 29px;
      cursor: pointer;
      font-family: $HelveticaNeueRegular;
      font-weight: normal;
      font-size: 16px;
      color:$black;
      strong{
        font-family: $HelveticaNeueMedium;
        font-weight: 500;
        display: block;
      }
    }
    .checkbox__text:before {
      content: '';
      position: absolute;
      top: 0px;
      left: 0;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background: $white;
      border: 1px solid #ebebeb;
      transition: .2s;
    }
    .checkbox__text:after {
      content: '';
      position: absolute;
      top: 6px;
      left: 6px;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background: $orange;
      transition: .2s;
      opacity: 0;
    }

    .checkbox input:checked + .checkbox__text:after {
      opacity: 1;
    }
    .checkbox input:checked +.checkbox__text{
      color: $orange;
    }


  }
  @media (max-width:1199px) {
    &__info{
      padding: 44px 50px;
    }
    &__list{
      padding: 0 50px 45px;
    }
  }
  @media (max-width:991px) {
    &__info{
      padding: 34px 60px 36px;
    }
    &__list{
      padding: 0 80px 45px 40px;
    }
