      margin-bottom: 39px;
    }
    &__subtitle{
      margin-bottom: 32px;
    }
    &__item{
      width: 100%;
      &:first-child{
        max-width: 100%;
        margin-bottom: 28px;
      }
    }

    &__dop-order-items{
      .checkbox{
        width: 33.333%;
      }
    }
  }
  @media (max-width: 767px) {
    padding-top: 24px;
    padding-bottom: 40px;
    margin-bottom: 40px;
    &__title{
      font-size: 24px;
      margin-bottom: 22px;
    }
    &__info{
      padding: 26px 20px 24px;
      margin-bottom: 24px;
    }
    &__subtitle{
      font-size: 24px;
      margin-bottom: 23px;
    }
    &__payment{
      margin-bottom: 24px;
    }
    &__list{
      padding: 0 0px 20px;
      margin-bottom: 25px;
    }
    &__dop-order-items{
      margin-bottom: 12px;
      .checkbox{
        width: 100%;
        margin-bottom: 26px;
      }
      .checkbox__text{
        strong{
          display: inline-block;
        }
      }
    }
  }

}

