.product-gallery-slider__item-img{
  display: block;
  margin-bottom: 19px;
  margin-left: 10px;
  margin-right: 10px;
  position: relative;
  &:before{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: $black;
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s ease;
  }
  &:after{
    content: "";
    position: absolute;
    width: 36px;
    height: 36px;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    background: url("../images/lupa.png") center no-repeat;
    opacity: 0;
  }
  &:hover{
    &:before{
      opacity: 0.5;
    }
    &:after{
      opacity: 1;
    }
  }
  @media (max-width:991px) {
   /* img{
      max-height: 338px;
    }*/
  }
  @media (max-width:767px) {
    margin-left: 0px;
    margin-right: 0px;
     img{

       height: 200px;
       object-fit: cover;
       margin-left: auto;
       margin-right: auto;
       width: 100%;
       max-width: 370px;
     }
  }
}

