$height-header:72px;

.header{
  height: $height-header;
  background: #0d0d0d;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  &__wrap{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    height: $height-header;
  }
  &__logo{}
  &__menu{
    width: 58%;
    padding: 0 8px;
  }
  &__menu-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 25px;
    li{
      a{
        color: #aaa;
        font-size: 14px;
        text-decoration: none;
        transition: all 0.3s ease;
        &:hover{
          color: #fff;
        }
      }
    }
  }
  &__phone{
    //width: 14%;
    font-size: 14px;
    font-weight: 400;
    color: $orange;
    text-align: center;
    transition: all 0.3s ease;
    padding: 0 35px 0 0;//0 25px 0 10px
    &:hover{
      color: $white;
    }
  }
  &__others{
    width: 128px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  &__info{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  &__compare{
    //padding: 0 10px;
    &-icon{}
    &-number{
      font-family: $HelveticaNeueLight;
      font-size: 16px;
      color:#aaaaaa;
      position: relative;
      top: 3px;
    }
  }
