  &__lang{
    //padding: 0 20px 0;
    font-size: 14px;
    font-weight: 300;
    color: $white;
    text-transform: uppercase;
    &:hover{
      color: $orange;
    }
  }
  &__menu-toogle{
    //padding: 0 0px 0 8px;
    width: 30px;
    height: 23px;
    position: relative;
    margin-left: 32px;
    //margin-left: 19px;
    &:before{
      content: "";
      position: absolute;
      width: 30px;
      height: 23px;
      //background: url("../images/icon-menuNew.png") no-repeat center;
      background: url("../images/menu.svg") no-repeat center;
    }
    &.active{
      &:before{
        background: url("../images/icon-menuClose.png") no-repeat center;
      }
    }
    &:hover{
      cursor: pointer;
    }
  }
  @media (max-width:1199px) {
    &__logo{
      max-width: 110px;
    }
    &__menu{
      width: 52%;
    }
    &__menu-list{
      padding: 0 10px;
    }
    &__phone{
      padding: 0 20px 0 0;
    }
    &__others{
      width: 110px;
    }
    &__menu-toogle{
      margin-left: 20px;
    }
  }
  @media (min-width:992px) and (max-width: 1199px) {
    &__menu-list{
      li{
        &.d-none1024{
          display: none;
        }
      }
    }
    &__others{
      margin-left: 43px;
    }
    &__menu{
      width: 46%;
      margin-left: 14px;
    }
  }
  @media (min-width: 768px) and (max-width: 991px) {
    &__menu-toogle{
      margin-left: 40px;
    }
    &__others{
     width: 145px;
      margin-left: 21px;
    }
  }
  @media (max-width:991px) {
    &__menu{
      display: none;
    }
  }
  @media (max-width:767px) {
    &__search{
      display: none;
    }
    &__lang{
      display: none;
    }
    &__others{
      width: 34px;
      margin-left: 23px;
    }
    &__phone{
      width: 21px;
      height: 21px;
      max-width: 21px;
      background: url("../images/telephone.svg") no-repeat center;
      background-size: cover;
      span{
        display: none;
      }
    }
  }
}
