.partners-items{
  display: flex;
  flex-wrap: wrap;
  //justify-content: space-between;
  border: 1px solid #f0f0f0;
  border-bottom: 0;
  position: relative;
  &:before{
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background: #f0f0f0;
    bottom: 0;
    z-index: 1;
  }
  @media (max-width:767px) {
    border: 0;
    &:before{
      opacity: 0;
    }
  }
}
.partners-item{
  display: flex;
  flex-wrap: wrap;
  flex-flow: column;

  justify-content: center;
  width: 25%;
  height: 215px;
  //border: 0.5px solid #f0f0f0;
  position: relative;
  text-align: center;
  transition: all 0.3s ease;

  &__foto{
    padding: 0 15px;
    transition: all 0.3s ease;
    height: 102px;
    margin-bottom: 13px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    img{
      height: auto;
      max-height: 100%;
      width: auto;
      object-fit: contain;
    }
  }
  &__title{
    //position: absolute;
    //bottom: 30px;
    left: 0;
    right: 0;
    margin: auto;
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 14px;//16
    color: $orange;
    transition: all 0.3s ease;
    span{
      position: relative;
      &:before{
        content: "";
        position: absolute;
        width: 0;
        height: 1px;
        bottom: 0;
        left: 0;
        background: $orange;
        transition: width 0.3s ease;
      }
    }
  }
  &:hover{

    .partners-item__title{
      text-decoration: underline;
     /* span{
        &:before{
          width: 100%;
        }
      }*/
    }
  }
  @media (min-width:768px) and (max-width: 991px) {
    border-right: 1px solid #f0f0f0;
    border-bottom: 1px solid #f0f0f0;
    &:nth-child(3n){
      border-right: 0;
    }
    &:nth-last-child(-n+3){
      //border-bottom:0;
    }

  }
  @media (min-width:992px) {
    border-right: 1px solid #f0f0f0;
    border-bottom: 1px solid #f0f0f0;
    &:nth-child(4n){
      border-right: 0;
    }
    &:nth-last-child(-n+4){
      //border-bottom:0;
    }
  }
 
  @media (max-width:991px) {
    width: 33.3333%;
  }
  @media (max-width:767px) {
    width: 50%;
    height: 160px;

    border: 0;
    border-bottom: 1px solid #f0f0f0;
    &__foto{
      height: 70px;
    }
  }
  @media (max-width:499px) {
    width: 100%;
  }
}
.partners-btn{
  display: none;
  margin-top: 40px;
  @media (max-width:767px) {
    display: block;
  }
}
