.news-detail{
  margin-top: 40px;
  &__header{
    height: 401px;
    position: relative;
    padding: 0 100px;
    margin-bottom: 58px;
  }
  &__header-info{
    position: absolute;
    bottom: 40px;

  }
  &__header-title{
    font-family: $HelveticaNeueThin;
    font-weight: 100;
    font-size: 35px;
    color:$white;
    margin-bottom: 10px;
  }
  &__header-date{
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 10px;
    color: #b3b3b3;
    text-transform: uppercase;
    letter-spacing: 2px;
  }
  &__content{
    padding: 0 100px 40px;

    border-bottom: 1px solid #f8f8f8;
    .content-social{
      display: block;
      margin-bottom: 50px;
      &__title{
        margin-bottom: 18px;
      }
      &__link{
        padding: 0;
        & + a{
          margin-left: 17px;
        }
      }
    }
    .content{
      padding-right: 100px;
    }
  }
  @media (max-width:1199px) {
    &__content{
      padding: 0 0 40px;
    }
  }
  @media (max-width:991px) {
    margin-top: 20px;
    &__header{
      padding: 0 30px;
      margin-bottom: 38px;
    }
    &__header-info{
      bottom: 30px;
    }
    &__header-title{
      font-size: 24px;
    }
    &__content{
      padding: 0 0 40px;
      .content-social{
        //display: none;
        margin-bottom: 35px;

      }
      .content{
        padding-right: 0px;
      }
    }
  }

  @media (max-width:767px) {
    &__header{
      padding: 0 20px;
    }
    &__header-info{
      bottom: 20px;
    }
    &__content{
      .content-social{
        &__links{
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
        }
        &__link{
          margin-left: 0;
          &+ a{
            margin-left: 0;
          }
        }
      }
    }
  }
}
.content-slider{
  margin-top: 51px;
  padding: 0 100px 45px;

  .slick-arrow{
    position: absolute;
    top: 0;
    bottom: 0;

    margin: auto;
    width: 41px;
    height: 138px;
    z-index: 2;
    opacity: 1;
    cursor: pointer;
    transition: all 0.3s ease;
    &.prevArrow{
      left: 0px;
      background: url("../images/sl-arrowGreyH138.png") no-repeat;

      &:hover{
        background: url("../images/sl-arrowOrangeH138.png") no-repeat;
      }
    }
    &.nextArrow{
      right: 0px;
      background: url("../images/sl-arrowGreyH138.png") no-repeat;
      transform: rotate(180deg);
      &:hover{
        background: url("../images/sl-arrowOrangeH138.png") no-repeat;
        transform: rotate(180deg);
      }
    }
  }
  @media (max-width:991px) {
    padding: 0 0 45px ;
    margin-top: 34px;
  }
}
.news-detail-navigation{
  margin-top: 31px;
  border-bottom: 1px solid #f8f8f8;
  padding-bottom: 30px;
  margin-bottom: 40px;
  &__link{
    position: relative;
    font-family: $HelveticaNeueBold;
    font-weight: bold;
    font-size: 10px;
    color:$black;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: all 0.3s ease;

    &:before{
      content: "";
      position: absolute;
      background: url("../images/arrow-h17.png") no-repeat center;
      width: 10px;
      height: 17px;
      top:-1px;
      bottom: 0;
      margin: auto;
    }
    &--left{
      padding-left: 30px;
      &:before{
        left: 0;
      }
    }
    &--right{
      padding-right: 30px;
      &:before{
        right: 0;
        transform: rotate(180deg);
      }
    }
    &:hover{
      color: $orange;
    }
  }
  @media (max-width:767px) {
    &__link{
      span{
        display: none;
      }
    }
  }
}
