.footer{
  background: #303030;
  padding-top: 58px;
  padding-bottom:38px;
  @media (max-width:991px) {
    padding-top: 15px;
    padding-bottom: 31px;
  }
  @media (min-width:768px) and (max-width: 991px) {
    padding-top: 19px;
    border-bottom: 1px solid #676767;
  }
}

.footer-cont {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  &__logo{
    .logo{}
  }
  &__copyright{
    font-size: 14px;
    color:#545454;
    margin-bottom: 38px;
  }
  @media (min-width:992px) {
    &__logo{
      margin-top: -4px;
    }
  }
  @media (max-width:1199px) {
    &__copyright{
      margin-bottom: 26px;
      span{
        display: block;
      }
    }
    .soc-bl{
      a{
        width: 25px;
        height: 24px;
        i{
          width: 25px;
          height: 24px;
          background-size: cover !important;
        }
      }
    }
  }
  @media (max-width: 991px){
    .soc-bl{
      a{
        width: 31px;
        height: 30px;
        margin-right: 18px;
        i{
          width: 31px;
          height: 30px;
          background-size: cover !important;
        }
      }
    }
  }
  @media (min-width: 768px) and (max-width: 991px){
    &__copyright{
      margin-bottom: 20px;
      span{
        display: inline;
      }
    }
    .soc-bl{
      a{
        width: 33px;
        height: 32px;
        margin-right: 18px;
        i{
          width: 33px;
          height: 32px;
          background-size: cover !important;
        }
      }
    }
  }
  @media (max-width:767px) {
    .soc-bl{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

  }
  @media (max-width:565px) {
    flex-flow: column;
    //align-items: center;
    text-align: center;
    &__logo{
      text-align: center;
    }
    &__copyright{
      span{
        display: inline;
      }
    }
    .soc-bl{
      margin-bottom: 27px;
    }
  }
}

.footer-nav{
  //margin-top: -3px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 42%;
  ul{
    list-style: none;
    padding: 0;
    display: inline-block;
    vertical-align: top;
    &:first-child{
      margin-left: 0;
    }
    li{
      margin: 0 0 19px;
      font-family: $HelveticaNeueRegular;
      &:last-child {
        margin-bottom: 0;
      }
      a{
        color: #aaa;
        font-size: 14px;
        line-height: 20px;
        transition: all 0.3s ease;
        -webkit-font-smoothing: antialiased;
        &:hover{

          color: $orange;
        }
      }
    }
  }
  @media (max-width:991px) {
    display: none;
  }
}
.footer-contact{
  &__phone{
    display: block;
    font-family: $HelveticaNeueThin;
    font-size: 30px;
    line-height: 30px;
    margin-bottom: 10px;
    letter-spacing: -.7px;
    text-decoration: none;
    color: $white;
    transition: all 0.3s ease;

    span{
      display: block;
      position: relative;


    }
    &:hover{
      color: $orange;
      span{

      }
    }

  }
  &__callback{
    color:$orange;
    display: block;
    margin-bottom: 32px;
    span{
      position: relative;
      &:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        bottom: 0;
        left: 0;
        background: $orange;
        transition: width 0.3s linear;
      }
    }
    &:hover{
      color: $orange;
      span{
        &:before{
          width: 0%;
        }
      }
    }
  }
  &__file{
    display: inline-block;
    font-size: 12px;
    line-height: 24px;
    overflow: hidden;
    .icon{
      float: left;
      margin-top: 2px;
      width: 27px;
      height: 35px;
      background: url("../images/icon-file.png") no-repeat center;
      transition: all 0.3s ease;
    }
    &:hover{

      .footer-contact__info{
        div{
          &:first-child{
            &:before{
              //width: 0;
            }
          }
        }
      }
    }
  }
  &__info{
    margin-left: 37px;
    display: block;
    div{
      &:first-child{
        color: $orange;
        font-size: 14px;
        line-height: 17px;
        margin-bottom: 3px;
        position: relative;
        &:before{
          content: "";
          position: absolute;
          width: 100%;
          height: 1px;
          bottom: 0;
          left: 0;
          background: $orange;
          transition: width 0.3s linear;
        }
      }
      &:last-child{
        font-size: 12px;
        color:#545454;
        text-align: left;
      }
    }
  }
  a{

  }
  @media (min-width: 768px) {
    text-align: right;
  }
  @media (min-width:768px) and (max-width: 991px) {
    text-align: right;
    margin-top: 4px;
    &__callback{
      margin-bottom: 16px;
    }
  }
  @media (max-width:565px) {
    &__phone{
      margin-bottom: 14px;
    }
    &__callback{
      margin-bottom: 27px;
    }

  }
}
.footer-menu{
  padding: 15px 0;
  border-top: 1px solid #282828;
  border-bottom: 1px solid #282828;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 30px 0 35px;
  nav{
    width: 100%;
  }
  &__list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    li{
      padding: 10px;
      a{
        font-size: 14px;
        font-weight: normal;
        color: $white;
      }

    }
  }
  @media (min-width:992px) {
    display: none;
  }
  @media (min-width:768px) and (max-width: 991px) {
    margin: 18px 0 23px;
    padding: 8px 0;
    &__list{
      justify-content: space-between;
    }
  }
  @media (max-width:767px) {
    margin: 24px 0 30px;
  }
  @media (min-width:565px) and (max-width: 767px) {

    &__list{
      justify-content: center;
      li{
        padding: 14px;
        &:first-child{
          padding-left: 0;
        }
        &:last-child{
          padding-right: 0;
        }
      }
    }
  }
  @media (max-width:564px) {
    padding: 15px 0;
    &__list{
      justify-content: center;
      margin-left: -21px;
      margin-right: -27px;
      li{
        padding: 10px 0;
        flex: 3 33.333%;
        text-align: center;
      }
    }
  }

}
.footer-filter{
  padding: 34px 0 15px;
  border-top: 1px solid #282828;
  border-bottom: 1px solid #282828;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 30px 0 35px;
  &__item{
    &:last-child{

    }
  }
  &__title{
    font-size: 10px;
    line-height: 20px;
    margin-bottom: 8px;
    color:#232323;
    text-transform: uppercase;
  }
  &__list{
    li{
      display: inline-block;
      vertical-align: top;
      margin: 0 0 20px 0;
      a{
        font-size: 14px;
        //color:#545454;
        color:$white;
        display: block;
        font-family: $HelveticaNeueLight;
        font-weight: 300;
        opacity: 0.3;
        text-shadow:none;
        -webkit-font-smoothing: antialiased;
        &:hover{
          opacity: 1;
          //color: $white;
        }
      }
      & + li{
        margin-left: 30px;
      }
    }
  }
  @media (min-width: 1200px) {
    margin: 39px 0 35px;
  }
  @media (max-width:1199px) {
    display: none;
  }
}
.footer-info{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  &__policy{
    a{
      font-size: 14px;
      color: #545454;
      position: relative;
      display: inline-block;
      span{
        position: relative;
        &:before{
          content: "";
          position: absolute;
          width: 100%;
          height: 1px;
          background: #545454;
          bottom: 0;
          transition: width 0.3s ease;
          left: 0;
        }
      }

      &:hover{
        color: #545454;
        span{
          &:before{
            width: 0;
          }
        }

      }
      & + a{
        margin-left: 37px;
      }
    }
  }
  &__dev{
    font-size: 14px;
    color: #545454;
    a{
      color: $orange !important;
      cursor: pointer;
      position: relative;
      &:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        background: $orange;
        bottom: 0;
        transition: width 0.3s ease;
        left: 0;
      }
      &:hover{
        &:before{
          width: 0;
        }
      }
    }
  }
  @media (min-width: 992px) and (max-width:1199px) {
    padding-top: 38px;
    margin-top: 38px;
    border-top: 1px solid #282828;
  }
  @media (max-width:991px) {
    &__policy{
      a{
        & + a{
          margin-left: 20px;
        }
      }
    }
  }
  @media (max-width:767px) {
    &__policy{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      width: 100%;
      a{
        //display: none;
        &:first-child{
          display: inline-block;
        }
        & + a{
          margin-left: 0;
        }
      }
    }
    &__dev{
      margin: 15px auto 0;
    }
  }
  @media (max-width:543px) {
    &__policy{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      a{
        &:first-child{
          width: 100%;
          margin-bottom: 13px;
          text-align: center;
        }
      }
    }
    &__dev{
      margin: 15px auto 0;
    }
  }
  @media (min-width:425px) and (max-width: 543px) {
    &__policy{
      justify-content: center;
      a{
        &:nth-child(2){
          width: 50%;
          text-align: center;
        }
        &:nth-child(3){
          width: 50%;
          text-align: center;
        }
      }
    }
  }
  @media (max-width:413px) {
    justify-content: center;
    &__policy{
      width: 100%;
      text-align: center;
    }
    &__dev{
      margin-top: 17px;
    }
  }
}




