.header-home-slider{
  margin-bottom: 50px !important;
  margin-top: 72px;
  &__item{
    display: flex !important;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center !important;
    text-align: center;
    height: 677px;
    position: relative;
    overflow: hidden;
  }
  video{
    min-width: 100%;
    min-height: 160%;
    width: auto; height: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    right: 0;
    @media (min-width: 992px) and(max-width: 1199px) {
     left: -279px;
    }
    @media (max-width:991px) {
      height: 500px;

    }
    @media (max-width:586px) {
      margin-left: -50%;
    }
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  &__foto{
    position: absolute;
    width: 100vw;
    height: 100%;

  }
  &__title{
    font-family: $HelveticaNeueThin;
    font-size: 52px;
    font-weight: 100;
    color:$white;
    text-transform: uppercase;
    margin-bottom: 15px;
  }
  &__subtitle{
    font-family: $HelveticaNeueLight;
    font-size: 24px;
    font-weight: 300;
    color:$white;
    margin-bottom: 33px;
  }
  @media (min-width: 768px) and (max-width: 991px) {
    &__item{
      height: 302px;
    }
    &__title{
      font-size: 34px;
      margin-bottom: 9px;
    }
    &__subtitle{
      font-size: 20px;
      line-height: 30px;
      max-width: 431px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 15px;
    }
    &.sl-white{
      .slick-dots{
        bottom: 20px;
      }
      .slick-arrow.prevArrow{
        left: 23px;
      }
      .slick-arrow.nextArrow{
        right: 23px;
      }
    }
  }
  @media (min-width:768px) {
    .home-acquaintance__video-play{
      display: none;
    }
  }
  @media (max-width:767px) {
    margin-bottom: 34px !important;
    .home-acquaintance__video-play{
      pointer-events: none;
    }

    &__item--video{
      .header-home-slider__content{
        pointer-events: none;
       .btn{
         display: none;
       }
      }
    }
    &__item{
      height: 300px;
    }
    &__title{
      font-size: 34px;
      margin-bottom: 12px;
    }
    &__subtitle{
      font-size: 18px;
    }
    &.sl-white{
      .slick-dots{
        bottom: 20px;
      }
    }
  }
}

//catalog
//  
.home-popular-projects{
  margin-bottom: 60px;
  @media (max-width:1199px) {
    margin-bottom: 40px;
  }
  @media (max-width:767px) {
    margin-bottom: 40px;
    .h2{
      text-align: center;
    }
  }
}
.catalog-slider{
  margin-bottom: 20px;
  .catalog-item{
    margin-bottom: 28px;
  }
  @media (max-width:991px) {
    margin-bottom: 40px;
  }
  @media (max-width:767px) {
    margin-bottom: 20px;
  }
}
.catalog-item {
  display: block;
  font-family: $HelveticaNeueRegular;
  font-weight: 400;
  max-width: 370px;
  margin: 0 auto 30px;

  &__foto{
    position: relative;
    overflow: hidden;
    &:before{
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background: rgba($black, 0.4);
      opacity: 0;
      z-index: 2;
      transition: all 0.3s ease;
    }
    img{
      transition: all 0.5s ease;
    }
  }
  &__info{
    position: absolute;
    bottom: 16px;
    padding: 0 28px;
    font-size: 24px;
    font-family: $HelveticaNeueThin;
    font-weight: 100;
    line-height: 35px;
    color: #fff;
    left: 0;
    z-index: 3;
  }
  &__title{
    font-size: 24px;
  }
  &__price{
    font-size: 35px;
    line-height: 1;
    sup{
      font-family: $HelveticaNeueThin;
      font-weight: 100;
      font-size: 20px;
    }
  }
  &__desc{
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
    border: 1px solid #f0f0f0;
    border-top: none;
    padding: 18px 30px 20px;
    &-bl{
      color: #000;
      font-size: 12px;
      line-height: 16px;
      width: 50%;
      &:last-child{
        text-align: right;
        border-left: 1px solid #f1f1f1;
      }
    }
    &-title{
      font-family:  $HelveticaNeueBold;//$HelveticaNeueCyrBlack
      font-size: 10px;
      font-weight: bold;
      color:$black;
      text-transform: uppercase;
      letter-spacing: 1.5px;
      margin-bottom: 13px;
    }
    &-sq{
      font-family:$HelveticaNeueRegular;
      font-weight: normal;
      font-size: 30px;
      color:$orange;
      span{
        font-size: 16px;

      }
      sup{
        font-size: 10px;
      }
    }
  }
  &:hover{
    .catalog-item__foto{
      &:before{
        opacity: 1;
      }
      img{
        transform: scale(1.1);
      }
    }
  }
  @media (min-width:1200px) {
    &__title{
      margin-bottom: 5px;
    }
  }
  @media (max-width:1199px) {
    &__info{
      padding: 0 10px;
    }
    &__price{
      font-size: 24px;
      sup{
        font-size: 15px;
      }
    }
    &__desc{
      padding: 18px 10px 20px;
      &-sq{
        font-size: 24px;
      }
    }
  }
  @media (max-width:991px) {
    &__info{
      padding: 0 20px;
    }
    &__desc{
      padding: 18px 20px 20px;
    }
  }
  @media (max-width:767px) {
    &__info{
      padding: 0 10px;
    }
    &__desc{
      padding: 18px 10px 20px;
    }
  }
}

//home-technology
.home-technology{
  margin-bottom: 60px;
  &__info{
    text-align: center;
    color:$white;
    padding: 50px 0;
  }
  &__icon{
    margin-bottom: 20px;
  }
  &__title{
    font-family: $HelveticaNeueMedium;
    font-size: 24px;
    font-weight: 500;
    color:$white;
    margin-bottom: 33px;
  }
  &__desc{
    font-family: $HelveticaNeueLight;
    font-size: 18px;
    font-weight: 300;
    line-height: 30px;
    color:#dedede;
    max-width: 432px;
    margin: 0 auto 45px;

  }
  &__btn{}
  &__advantages-items{
    padding-top: 50px;
    position: relative;
    &:before{
      content: "";
      position: absolute;
      width: 1px;
      height:calc(100% - 50px);
      background: $orange;
      left: 0;
      right: 0;
      margin: auto;
    }
  }
  &__advantages-item{
    text-align: center;
    width: 50%;
    padding: 0 10px;
    max-width: 270px;
    margin-left: auto;
    margin-right: auto;
    &:nth-last-child(-n+2){
      margin-top: 73px;
    }
  }
  &__advantages-icon{
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
    svg, img{
      height: 50px;
      margin-left: auto;
      margin-right: auto;
    }
  }
  &__advantages-title{
    font-family: $HelveticaNeueMedium;
    font-size: 18px;
    font-weight: 500;
    color: $orange;
    margin-bottom: 9px;
  }
  &__advantages-desc{
    font-family: $HelveticaNeueRegular;
    font-size: 18px;
    font-weight: normal;
    color: $orange;
    sup{
      font-size: 13px;
    }
  }
  &__advantages-number{

    margin-bottom: 20px;
    svg, img{
      height: 55px;
    }
  }
  @media (max-width:1199px) {
    &__advantages-item{

      margin-left: auto;
      margin-right: auto;
    }
    &__advantages-title{
      font-size: 17px;
    }
    &__advantages-desc{
      font-size: 17px;
    }
    &__desc{
      font-size: 17px;
    }
  }
  @media (max-width:991px) {
    &__desc{
      max-width: 600px;
    }
  }
  @media (max-width:767px) {
    margin-bottom: 40px;
    &__advantages-items{
      padding-top: 40px;
    }
    &__info{
      padding: 40px 15px;
    }
    &__icon{
      margin-bottom: 20px;
    }
    &__title{
      margin-bottom: 12px;
    }
    &__desc{
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 35px;
    }
  }
  @media (max-width:549px) {
    &__advantages-items{
      text-align: center;
      padding-bottom: 25px;
      &:before{
        opacity: 0;
      }
    }
    &__advantages-item{
      width: 100%;
      margin-left: auto;
      &:nth-last-child(-n+2){
        margin-top: 0;
      }
    }
  }

}

//home-acquaintance
.home-acquaintance{
  padding-top: 51px;
  padding-bottom: 186px;
  .h2{
    margin-bottom: 30px;
  }
  &__desc{
    font-family: $HelveticaNeueLight;
    font-weight: 300;
    font-size: 18px;
    line-height: 30px;
    color: $white;
    text-align: center;
    max-width: 970px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 44px;
  }
  &__video{
    text-align: center;
    display: block;
    margin-bottom: 54px;
    &:hover{
      text-decoration: none;
    }
    &-play{
      display: inline-block;
      top:6px;
    }
    &-text{
      display: inline-block;
      font-family: $HelveticaNeueMedium;
      font-size: 10px;
      line-height: 18px;
      color: $white;
      letter-spacing: 2px;
      text-transform: uppercase;
      text-align: left;
      margin-left: 20px;
    }
  }
  @media (max-width:991px) {
    padding-top: 60px;
    padding-bottom: 60px;
    &__video{
      margin-bottom: 0;
    }
  }
  @media (max-width:767px) {
    padding-top: 36px;
    &__desc{
      font-size: 16px;
      line-height: 24px;
    }
  }
}

//home-project
.home-project{
  margin-top: -181px;
  margin-bottom: 52px;

  @media (max-width:991px) {
    margin-top: 0;
   .container{
     max-width: 100%;
     width: 100%;
     padding-left: 0;
     padding-right: 0;
   }
  }
  @media (max-width:767px) {
    margin-top: 0;
    margin-bottom: 35px;
  }
}
.home-project-items{
  background: $orange-lite;
  color: $white;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  &:after{
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background: $orange-lite;
    bottom: 0;
    left: 0;
    right: 0;
  }

}
.home-project-item{
  flex:0 0 25%;
  font-family: $HelveticaNeueMedium;
  font-size: 18px;
  color: $white;
  text-decoration: none;
  text-align: center;
  position: relative;
  padding-top: 41px;
  padding-bottom: 35px;
  transition: all 0.5s ease;
  &:before{
    content: "";
    position: absolute;
    width: 1px;
    height: 100%;
    background: $orange-dark;
    right: 0;
    top:0;
  }
  &:after{
    content: "";
    position: absolute;
    height: 1px;
    width: 100%;
    background: $orange-dark;
    right: 0;
    bottom:0;
  }
  &__icon{
    margin-bottom: 20px;
  }
  &__text{
    font-family: $HelveticaNeueMedium;
    font-size: 18px;
    line-height: 1.2;
  }
  &:nth-child(4n){
    &:before{
      opacity: 0;
    }
  }
  &:nth-child(-n+4){
    &:after{
      //opacity: 0;
    }
  }
  &:hover{
    background: $orange-dark;
    color:$white;
    text-decoration: none;
  }
  @media (min-width:768px) and (max-width: 991px) {
    padding-left: 20px;
    padding-right: 20px;
  }
  @media (max-width:991px) {
    &__text{
      font-size: 16px;
    }
  }
  @media (max-width:767px) {
    flex:0 0 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: center;
    padding: 20px 10px;
    text-align: left;
    &__icon{
      margin-bottom: 0;
      width: 58px;
      text-align: left;
      svg, img{
        height: 35px;
      }
    }
  }
}

//home-reviews
.home-reviews{
  .h2{
    margin-bottom: 42px;
  }
  @media (max-width:767px) {
    .h2{

      margin-bottom: 33px;
    }
  }
  @media (max-width:370px) {
    .h2{
      text-align: center;
    }
  }
}

.home-reviews-slider {
  //padding: 0 100px;
  padding-bottom: 50px;
  margin: 0 0 60px !important;
  .slick-arrow {
    &:hover {
      cursor: pointer;
    }
    &.prevArrow {
      width: 32px;
      height: 103px;
      background: url("../images/arrowh103-orange.png") no-repeat;
      z-index: 1;
      position: absolute;
      top: 20%;
      left: 0px;
      transform: rotate(180deg);
      transition: all 0.5s ease;
      &:hover {
        background: url("../images/arrowh103-black.png") no-repeat;
      }
    }
    &.nextArrow {
      width: 32px;
      height: 103px;
      background: url("../images/arrowh103-orange.png") no-repeat;
      z-index: 1;
      position: absolute;
      top: 20%;
      right: 0px;
      transition: all 0.5s ease;
      &:hover {
        background: url("../images/arrowh103-black.png") no-repeat;
      }
    }
  }

  &__item{}
  &__item-text{
    max-width: 500px;
    .name{
      font-family: $HelveticaNeueRegular;
      font-size: 24px;
      line-height: 24px;//26
      color: $black;
    }
    .title{
      font-family: $HelveticaNeueMedium;
      font-size: 10px;
      line-height: 36px;
      text-transform: uppercase;
      letter-spacing: 2px;
      color: #ff815c;
      margin-bottom: 13px;
    }
    .text{
      font-family: $HelveticaNeueRegular;
      font-size: 16px;
      line-height: 26px;
      color: $black;
    }
  }
  &__item-video{
    position: relative;
    max-width: 370px;
    .btn-play{
      position: absolute;
    }
  }
  @media (max-width:1199px) {

  }
  @media (max-width:991px) {
    padding-left: 60px;
    padding-right: 60px;
    &__item-text{
      margin-bottom: 23px;
    }

  }
  @media (max-width:767px) {
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 43px;
    margin-bottom: 45px !important;
    &__item-video{
      margin-bottom: 30px;
    }
    &__item-text{
      margin-bottom: 0;
    }
  }
}






