.page-dillers{
  @media (max-width:767px) {
    .main{
      margin-bottom: 0;
    }
  }
}
.dillers-wrap{
  @media (max-width:767px) {
    margin-top: -10px;
  }
}
.dillers{
  &__partners{
    padding: 60px 100px 70px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 73px;
    margin-top: 51px;

  }
  &__partners-item{
    width: 48%;
    &:first-child{
      width: 44%;
    }

  }
  &__partners-title{
    font-family: $HelveticaNeueThin;
    font-size: 35px;
    line-height: 48px;
    color:$white;
    strong{
      font-family: $HelveticaNeueBold;
      font-weight: bold;
    }
  }
  &__partners-desc{
    font-family: $HelveticaNeueLight;
    font-weight: 300;
    font-size: 18px;
    line-height: 27px;
    color:$white;
    margin-top: 5px;
  }
  &__partners-btn{
    width: 100%;
    margin-top: 53px;
  }
  &__advantages{

  }
  &__advantages-title{
    font-family: $HelveticaNeueThin;
    font-weight: 100;
    font-size: 35px;
    color:$black;
    max-width: 920px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    margin-bottom: 52px;
  }
  &__advantages-items{
    margin-bottom: 20px;
  }
  @media (max-width:1199px) {
    &__partners{
      padding: 60px 60px 70px;
    }

  }
  @media (min-width: 768px) and(max-width: 991px) {
    &__partners{
      padding: 60px 60px 70px;
    }
    &__partners-item{
      width: 100%;
      &:first-child{
        width: 100%;
      }
    }
    &__partners-title{
      font-size: 31px;
      line-height: 1.2;
      margin-bottom: 21px;
    }
    &__partners-btn{
      margin-top: 34px;
    }
  }
  @media (max-width:991px) {
    &__advantages-items{
      margin-bottom: 40px;
    }
  }
  @media (max-width:767px) {
    &__partners{
      margin-top: 31px;
      padding: 41px 15px 40px;
      margin-bottom: 37px;
    }
    &__partners-item{
      width: 100%;
      &:first-child{
        width: 100%;
      }
    }
    &__partners-title{
      font-size: 24px;
      line-height: 1.2;
      margin-bottom: 27px;
    }
    &__partners-desc{
      font-size: 16px;
    }
    &__partners-btn{
      margin-top: 35px;
    }
    &__advantages-title{
      font-size: 30px;
      font-family: $HelveticaNeueLight;
      font-weight: 300;
      margin-bottom: 33px;
    }

  }
  @media (max-width:575px) {
    &__partners{
      width: 100vw;
      margin-left: -15px;

    }
  }

}
.dillers-advantages-item{
  text-align: center;
  margin-bottom: 55px;
  &__icon{
    margin-bottom: 25px;
    img{
      margin-left: auto;
      margin-right: auto;
    }
  }
  &__title{
    font-family: $HelveticaNeueBold;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    color:$black;
    margin-bottom: 19px;
  }
  &__desc{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color:$black;
  }
  @media (max-width:991px) {
    margin-bottom: 42px;
  }
}
.dillers-info{
  padding: 112px 15px;
  margin-bottom: 60px;
  &__desc{
    font-family: $HelveticaNeueThin;
    font-weight: 100;
    font-size: 35px;
    color:$white;
    max-width: 996px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }
  @media (max-width:767px) {
    padding: 60px 15px;
    margin-bottom: 0;
    &__desc{
      font-size: 24px;
    }

  }
}
.dillers-form-wrap{
  @media (max-width:1199px) {
    background: #f6f6f6;
  }
}
.dillers-form{
  background: #f6f6f6;
  padding: 50px 95px 60px;
  &__title{
    margin-bottom: 28px;
  }
  @media (min-width:768px) and (max-width: 991px) {
    &__title{
      &.h2{
        font-size: 32px;
      }

    }
  }
  @media (max-width:1199px) {
    padding: 50px 0px 60px;
  }

  @media (max-width:767px) {
    padding: 34px 0px 38px;
    &__title{
      text-align: center;
    }
  }
}
.form-diller{
  display:flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  &.form-diller-contact{
    .form-diller__item{
      width: calc((100% - 80px)/3);
      @media (max-width: 991px) {
        width: calc((100% - 40px)/3);
      }
      @media (max-width: 767px) {
        width:100%;
      }
    }
  }

  &__item{
    display: flex;
    flex-wrap: wrap;
    width: calc((100% - 90px)/4);
    margin-bottom: 40px;
    input{
      font-family: $HelveticaNeueMedium;
      font-weight: 500;
      font-size: 14px;
      color:$black;
      width: 100%;
      height: 50px;
      line-height: 49px;
      padding: 0 15px 0 25px;

    }
  }
  &__title{
    width: 100%;
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 10px;
    color:#aeaeae;
    text-transform: uppercase;
    margin-bottom: 20px;
    span{
      color: #bb1a1a;
    }
  }

  textarea{
    width: 100%;
    height: 160px;
    padding: 18px 15px 0 25px;
  }
  &__textarea{
    width: 100%;
    margin-bottom: 37px;
  }
  &__others{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
  }
  &__file{
    padding-left: 35px;
    position: relative;
    height: 50px;

    display: inline-flex;
    align-items: center;
    &:before{
      content: "";
      position: absolute;
      width: 24px;
      height: 24px;
      left: 0;
      background: url("../images/clip.png") no-repeat center;
    }
    input{
      opacity: 0;
      position: absolute;
    }
  }
  &__file-desc{
    font-family: $HelveticaNeueRegular;
    font-weight: normal;
    font-size: 14px;
    color: $orange;
    cursor: pointer;
    position: relative;


    &:before{
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      background: $orange;
      bottom: 0;
    }
  }
  &__submit{
    width: 230px;
    display: inline-flex;
    input{
      width: 100%;
      max-width: 230px;
    }
  }
  &__file-desc{

  }

  ::-webkit-input-placeholder {
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color:$black;
  }
  ::-moz-placeholder         {
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color:$black;
  }/* Firefox 19+ */
  :-moz-placeholder           {
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color:$black;
  }/* Firefox 18- */
  :-ms-input-placeholder      {
    font-family: $HelveticaNeueMedium;
    font-weight: 500;
    font-size: 14px;
    color:$black;
  }

  @media (max-width:991px) {
    &__item{
      width: 48%;
      margin-bottom: 19px;
    }
    &__textarea{

    }
  }
  @media (max-width:767px) {
    &__item{
      width: 100%;
      margin-bottom: 19px;
    }
    &__title{
      margin-bottom: 10px;
    }
    &__textarea{
      margin-bottom: 20px;
    }
    &__others{

    }
    &__file{
      width: 100%;
      margin-bottom: 27px;
    }
    &__submit{
      width: 100%;
      justify-content: center;
    }

  }
}
